  import {BaseQuesClass} from './BaseClass'

  let inc = 0 ;
  /**
   * 填空题
   */
  class DescClass extends BaseQuesClass {
    constructor(design = true) {
      super();
      this.quesType = 'Desc';
      this.htmlTitle = '<p><strong>描述题</strong></p>';
      this.required = false;//必答题

    }

    /**
     * 答题界面样式配置
     */
    anInit() {
      super.anInit();
    }
  }
  export default DescClass




