<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        <table style="width:100%;border-spacing:5px;border-collapse:separate">
          <tr v-for="(row,rowIndex) in getConvertData">
            <td v-for="(col,colIndex) in row">
              <div v-html="col.htmlLabel"></div>
              <div style="height:5px"></div>
              <div style="display: flex">
                <el-input-number style="width:105px"
                                 v-model="survey.an[col.varName]" :min="0" :max="100"></el-input-number>
                <span style="color:#409EFF;width:65px">￥{{col.val}}</span>
                <span style="color:#409EFF">
                  ￥{{col.val * Fun.isNull(survey.an[col.varName]) ? 0 : survey.an[col.varName]}}
                </span>
              </div>
              <div style="height:5px"></div>
            </td>
          </tr>
        </table>
        <div style="height:5px"></div>
        <div style="text-align:right;font-weight:bold;color:#409EFF">合计：￥{{getSum}}&nbsp;&nbsp;</div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultMulGoodsView',
    components: {
      ViewQuesT
    },
    props: ['survey', 'ques'],
    data () {
      return {}
    },
    mounted(){
    },
    methods: {},
    computed: {
      getSum(){
        let sum = 0
        for (let item of this.ques.rows) {
          if (!this.Fun.isNull(this.survey.an[item.varName])) {
            sum += this.survey.an[item.varName] * item.val
          }
        }
        return sum;
      },
      /**
       * 转化二维数组
       */
      getConvertData(){
        let table = new Array()
        let currRow = null;
        for (let i = 0; i < this.ques.rows.length; i++) {
          if (i % this.ques.colspan == 0) {
            currRow = new Array()
            table.push(currRow)
          }
          currRow.push(this.ques.rows[i])
        }
        return table;
      },
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  table td {
    padding: 3px 5px 3px 5px;
    border: 1px solid #E6EBF5;
  }
</style>



