<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        <div style="height:5px"></div>
        <div style="padding:1px 3px">
          <el-cascader :style="{width:ques.width}"
                       expand-trigger="hover"
                       v-model="survey.an[ques.varName]"
                       :options="ques.rows"
                       :props="{value:'val',label:'htmlLabel',children:'rows'}">
          </el-cascader>
        </div>
        <div style="height:5px"></div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'RadioCascaderQuesViewD',
    components: {
      ViewQuesT,
    },
    props: ['survey', 'ques'],
    data () {
      return {
        val: undefined,
      }
    },
    mounted(){
    },
    methods: {

      changeCas(val){
        this.survey.an[this.ques.varName] = val[val.length - 1]
      },

    },
    computed: {},
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>

</style>



