<template>
  <div>
    <default-matrix-rate-view v-if="ques.style==''"
                               :survey="survey" :ques="ques">
    </default-matrix-rate-view>
    <icon-matrix-rate-view v-else-if="ques.style=='icon'"
                            :survey="survey" :ques="ques">
    </icon-matrix-rate-view>
  </div>
</template>

<script>
  import DefaultMatrixRateView from './view/DefaultMatrixRateView'
  import IconMatrixRateView from './view/IconMatrixRateView'
  export default {
    name: 'IndexMatrixRateView',
    components: {
      DefaultMatrixRateView,IconMatrixRateView
    },
    props: ['survey','ques'],
    data () {
      return {
      }
    },
    mounted(){

    },
    methods: {

    },
    computed: {},
    watch: {}
  }
</script>
