<template>
  <div>
    <view-ques-t-t :survey="survey" :ques="ques">
      <div slot="textQues">
        <div style="height:25px"></div>
        <div class="startBt" :style="{'text-align':ques.btTextAlign}" style="margin:auto;width:100%;">
          <el-button @click="start" style="border-color: transparent" size="medium" type="primary"
                      :style="{'background-color':ques.bgColor,width:ques.width1}"
                     v-html="ques.content"></el-button>
        </div>
      </div>
    </view-ques-t-t>
    <div style="height:10px"></div>
  </div>
</template>

<script>
  import ViewQuesTT from '../../ViewQuesTT'
  export default {
    name: 'DefaultStartView',
    components: {
      ViewQuesTT,
    },
    props: ['survey', 'ques'],
    data () {
      return {
      }
    },

    mounted(){
    },
    methods: {
        start(){
          this.survey.currPageIndex = 0 ;
        }
    },
    computed: {
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .ques div.startBt button p{
    margin: 0 3px;
  }
</style>
<style scoped>



</style>



