import {BaseOptionClass, BaseQuesClass} from "./BaseClass";
import Fun from "../Fun";

/**
 * 多选题选项
 */
class CheckboxVoteOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.varName = 'T_'+Fun.getInc();
    this.fieldName = this.varName;
    this.dft = false;//默认选中
    this.noRandom = false;//选项固定，在使用随机显示选项才起作用
    this.imgUrl = 'resource/img/logo2.png';
  }
}
/**
 * 多选题
 */
class CheckboxVoteClass extends BaseQuesClass {
  constructor(design = true) {
    super();
    this.quesType = 'CheckboxVote';
    this.htmlTitle = '<p><strong>多选投票题</strong></p>';
    this.random = false;//选项随机
    this.optionSumShow = true ;//是否选项数限制提示，
    this.min = 0;//最少个数
    this.max = 0;
    this.width = '' ;
    this.imgWidth = '' ;//图片宽度
    this.imgHeight = '';//
    this.imgShape = '';
    this.horCol = '';//每行显示列数

    this.pcHorCol = '2' ;
    this.phoneHorCol = '2' ;

    this.pcImgShape = 'img-rounded' ;
    this.phoneImgShape = 'img-rounded' ;

    this.pcVoteType = 'pic' ;
    this.phoneVoteType = 'pic' ;

    this.pcWidth = '60%' ;
    this.phoneWidth = '100%' ;

    this.pcImgWidth = '150px' ;
    this.pcImgHeight = '150px' ;

    this.phoneImgWidth = '110px' ;
    this.phoneImgHeight = '110px' ;

    this.rows = []
    if (design) {//设计问题
      this.init();
    }
  }

  /**
   * 验证答题
   * @param an
   * @returns {boolean}
   */
  anValid(an) {
    if (!this.required) {
      return
    }
    let sum = 0;//选择的数量
    for (let item of this.rows) {
      if (!Fun.isNull(an[item.varName])) {
        sum++;
      }
    }
    if (sum == 0) {
      this.errInfo = "这道题必须回答哦！"
      return false;
    }
    if (this.anHasOptionSum()) {//选项数量验证
      if (this.min > sum || this.max < sum) {
        this.errInfo = "选项个数不满足要求！"
        return false;
      }
    }
    this.errInfo = ""
    return true;
  }

  /**
   * 答题界面样式等初始配置
   */
  anInit() {
    super.anInit();
    if (this.device == 'pc') {
      this.width = this.pcWidth ;
      this.imgWidth = this.pcImgWidth ;
      this.imgHeight = this.pcImgHeight ;
      this.imgShape = this.pcImgShape ;
      this.horCol = this.pcHorCol ;
    }else{
      this.width = this.phoneWidth ;
      this.imgWidth = this.phoneImgWidth ;
      this.imgHeight = this.phoneImgHeight ;
      this.imgShape = this.phoneImgShape ;
      this.horCol = this.phoneHorCol ;
    }
  }

  getVarNames(){
    let varSet = new Set();
    for (let item of this.rows) {
      varSet.add(item.varName)
    }
    return varSet
  }


  /**
   * 答题时选项随机
   */
  anRandom(){
    super.anRandom();
    if (this.random) {
      Fun.optionRandom(this.rows)
    }
  }

  /**
   * 是否配置了选项数量控制
   * @returns {boolean}
   */
  anHasOptionSum() {
    if (this.max != 0 && (this.min <= this.max)) {
      if (this.max <= this.rows.length) {
        return true;
      }
    }
    return false;
  }

  anOptionSumInfo() {
    let start = this.min > 0 ? '请选择' + this.min + '-' : '最多选择'
    return '[' + start + this.max + '项]'
  }


  init() {
    let labels = ['<p>很不同意</p>', '<p>不同意</p>', '<p>一般</p>', '<p>同意</p>', '<p>很同意</p>']
    let rate = null
    for (let label of labels) {
      rate = new CheckboxVoteOptionClass();
      rate.htmlLabel = label
      this.rows.push(rate)
    }
  }

  addOption(index) {
    let option = new CheckboxVoteOptionClass();
    option.htmlLabel = "<p>选项" + (this.rows.length + 1)+"</p>";
    if (index == -1) {//尾部插入
      this.rows.push(option)
    } else {//之后插入
      this.rows.splice(index + 1, 0, option);
    }
  }

  delOption(index) {
    if (this.rows.length > 1) {
      this.rows.splice(index, 1)
    }
  }

  batchAddOptions(batchLabels) {
    batchLabels.forEach(label => {
      let option = new CheckboxVoteOptionClass();
      option.htmlLabel = label;
      this.rows.push(option)
    })
  }

  /**
   * 获取批量显示的选项标签,用于打开批量增加选项对话框的默认值
   */
  getBatchLabels() {
    let htmlLabels = []
    this.rows.forEach((option) => {
      let htmlLabel = option.htmlLabel;
      if (!Fun.isNull(htmlLabel)) {
        htmlLabels.push(htmlLabel)
      }
    })
    return htmlLabels;
  }

  /**
   * 上下移动选项
   * @param index
   * @param flag
   */
  moveOption(index, flag = 'pre') {
    let currOption = this.rows[index];
    if (flag === 'pre') {//上移
      this.rows.splice(index, 1);//删除
      this.rows.splice(index - 1, 0, currOption);//再插入
    } else {
      this.rows.splice(index, 1);//删除
      this.rows.splice(index + 1, 0, currOption);//再插入
    }
  }
}
export default CheckboxVoteClass



