import {BaseOptionClass, BaseQuesClass} from "./BaseClass";
import Fun from "../Fun";

let inc = 0;
/**
 * 选项
 */
class MatrixScaleOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.varName = 'I_'+Fun.getInc();
    this.fieldName = this.varName;
    this.htmlGroup = '';
    this.noRandom = false;//选项固定，在使用随机显示选项才起作用
  }
}

/**
 * 题
 */
class MatrixScaleClass extends BaseQuesClass {
  constructor(design=true) {
    super();
    this.quesType = 'MatrixScale';
    this.htmlTitle = '<p><strong>比重题</strong></p>';
    this.random = false;//选项随机
    this.width = '' ;
    this.width1 = '';//标签宽度

    this.pcWidth = '800px' ;
    this.pcWidth1 = '350px' ;

    this.phoneWidth = '100%' ;
    this.phoneWidth1 = '30%' ;
    this.minVal = 0;
    this.maxVal = 100 ;
    this.rows = [];
    if(design){//设计问题
      this.init();
    }
  }
  /**
   * 答题界面样式等初始配置
   */
  anInit(){
    super.anInit();
    if (this.device == 'pc') {
      this.width = this.pcWidth ;
      this.width1 = this.pcWidth1 ;
    }else{
      this.width = this.phoneWidth ;
      this.width1 = this.phoneWidth1 ;
    }
  }
  /**
   * 验证答题
   * @param an
   * @returns {boolean}
   */
  anValid(an){
    super.anValid(an);
    if (!this.required) {
      return true
    }
    let sum = 0
    for (let item of this.rows) {
      sum +=an[item.varName] ;
    }
    if (sum!=100) {
      this.errInfo = "总比重合计需要等于100！"
      return false
    }
    this.errInfo = ""
    return true
  }
  /**
   * 答题时选项随机
   */
  anRandom(){
    super.anRandom();
    if (this.random) {
      Fun.optionRandom(this.rows)
    }
  }

  init(){
    let labels = ['<p>外观</p>','<p>性能</p>']
    let rate = null
    for(let label of labels){
      rate = new MatrixScaleOptionClass();
      rate.htmlLabel = label
      this.rows.push(rate)
    }
  }
  getVarNames() {
    let varSet = new Set();
    for (let item of this.rows) {
      varSet.add(item.varName)
    }
    return varSet
  }

  addOption(index) {
      let option = new MatrixScaleOptionClass();
      option.htmlLabel = "<p>选项" + (this.rows.length + 1)+"</p>";
      if (index == -1) {//尾部插入
        this.rows.push(option)
      } else {//之后插入
        this.rows.splice(index + 1, 0, option);
      }
  }

  delOption(index) {
      if (this.rows.length > 1) {
        this.rows.splice(index, 1)
      }
  }

  batchAddOptions(batchLabels) {
      batchLabels.forEach(label => {
        let option = new MatrixScaleOptionClass();
        option.htmlLabel = label;
        this.rows.push(option)
      })
  }

  /**
   * 获取批量显示的选项标签,用于打开批量增加选项对话框的默认值
   */
  getBatchLabels() {
      let htmlLabels = []
      this.rows.forEach((option) => {
        let htmlLabel = option.htmlLabel;
        if (!Fun.isNull(htmlLabel)) {
          htmlLabels.push(htmlLabel)
        }
      })
      return htmlLabels;
  }

  /**
   * 上下移动选项
   * @param index
   * @param flag
   */
  moveOption(index,flag = 'pre') {
      let currOption = this.rows[index];
      if (flag === 'pre') {//上移
        this.rows.splice(index, 1);//删除
        this.rows.splice(index - 1, 0, currOption);//再插入
      } else {
        this.rows.splice(index, 1);//删除
        this.rows.splice(index + 1, 0, currOption);//再插入
      }
  }
}
export default MatrixScaleClass




