<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        <div style="height:5px"></div>
        <span style="margin-left:3px"></span>
        <kk-file :preview="false" :imme="true" :accept="accept" :name="'file'"
                 :browserLabel="'选择文件(小于'+ques.maxSize+'M)'" :size="ques.maxSize*1024*1024"
                 @fileChange="fileChange" @uploadFile="uploadFile"
                 :callError="true" @errorTip="errorTip"></kk-file>
        <div v-show="ques.typesShow&&accept.length>0" style="color: #409EFF;word-wrap: break-word ">
          允许上传的文件：[{{accept}}]
        </div>
        <div style="height:5px"></div>
        <div v-if="show&&survey.an[ques.varName]!=undefined" style="color:#67C23A;font-weight: bold">
          上传文件成功
        </div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  import KkFile from '@/components/common/KkFile'
  export default {
    name: 'DefaultFileView',
    components: {
      ViewQuesT, KkFile,
    },
    props: ['survey', 'ques'],
    data () {
      return {
        show: false,
      }
    },
    mounted(){
    },
    methods: {
      /**
       * 错误信息提示
       */
      errorTip(errObj){
        if (errObj.code == 0) {
          this.ques.errInfo = '最大允许文件大小：' + this.ques.maxSize + 'M！'
        } else if (errObj.code == 1 || errObj.code == 2) {
          this.ques.errInfo = '上传文件类型错误！'
        }
        this.show = false
        this.$set(this.survey.an, this.ques.varName, undefined)
      },
      /**
       * 获取当前选择文件的相关属性
       * @param fileObj
       */
      fileChange(fileObj){
        this.ques.errInfo = ''
        this.survey.an[this.ques.varName] = undefined
      },
      /**
       * 上传文件
       * @param formdata
       */
      uploadFile(formdata){
        this.show = false;
        if(!this.Fun.isNull(this.accept)){
          formdata.append('exts', this.accept);//同时上传支持的扩展名
        }
        let _reqObj = {url: this.Fun.uploadFileApi(), params: formdata}
        this.SERVICE.post(_reqObj, res => {
          this.survey.an[this.ques.varName] = res.data.res.localUrl
          this.$nextTick(
            () => {
              this.show = true;
            }
          )
        }).catch((error) => {
         // console.log(error)
          this.show = true;
        })

        //console.log("====" + JSON.stringify(this.survey.an))
        //formdata.append('fileName', '');//同时上传其它元素
//        this.$http.post("demo/uploadFile", formdata)
//          .then(res => {
//            console.log(res.data);
//          }).catch(err => {
//          console.log(err);
//        });
      },

    },
    computed: {
      accept(){
        let accept = ''
        for (let i = 0; i < this.ques.fileTypes.length; i++) {
          if (i > 0) {
            accept += ','
          }
          accept += this.ques.fileTypes[i]
        }
        return accept
      }
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  table td {
    padding: 0 1px;
  }
</style>



