import {BaseQuesClass,BaseOptionClass} from './BaseClass'
import Fun from '../Fun'
let inc = 0 ;
  /**
   * 多选题选项
   */
  class MulSortOptionClass extends BaseOptionClass {
    constructor() {
      super();
      this.varName = 'N_'+Fun.getInc();
      this.varNameT = 'T'+this.varName;
      this.fieldName = this.varName;
      this.fieldNameT = this.varNameT;
      this.htmlGroup = '';
      this.noRandom = false;//选项固定，在使用随机显示选项才起作用
      this.fillFlag = false; //允许填空
      this.fillRequired = false;//允许填空必填
    }
  }
  /**
   * 多选题
   */
  class MulSortClass extends BaseQuesClass {
    constructor(design=true) {
      super();
      this.quesType = 'MulSort';
      this.htmlTitle = '<p><strong>排序题</strong></p>';
      this.random = false;//选项随机
      this.width = '' ;
      this.width1 = '';//标签宽度

      this.pcWidth = '45%' ;
      this.pcWidth1 = '20%' ;

      this.phoneWidth = '100%' ;
      this.phoneWidth1 = '45%' ;
      this.rows = []
      if(design){//设计问题
        this.init();
      }
    }


    /**
     * 验证答题
     * @param an
     * @returns {boolean}
     */
    anValid(an) {
      super.anValid(an);
      if (!this.required) {
        return true
      }
      let sum = 0;//填写的数量
      for (let item of this.rows) {
        if (!Fun.isNull(an[item.varName])) {
          sum++
        }
      }
      if (sum < this.rows.length) {
        this.errInfo = "这道题必须回答哦！"
        return false
      }
      for (let item of this.rows) {
        if (item.fillFlag && item.fillRequired) {
          if (Fun.isNull(an[item.varNameT])) {
            this.errInfo = "标注*文本框必须填写！"
            return false;
          }
        }
      }
      this.errInfo = ""
      return true
    }
    /**
     * 答题界面样式配置
     */
    anInit(){
      super.anInit();
      if (this.device == 'pc') {
        this.width = this.pcWidth ;
        this.width1 = this.pcWidth1 ;
      }else{
        this.width = this.phoneWidth ;
        this.width1 = this.phoneWidth1 ;
      }
    }
    /**
     * 答题时选项随机
     */
    anRandom(){
      super.anRandom();
      if (this.random) {
        Fun.optionRandom(this.rows)
      }
    }

    getVarNames(){
      let varSet = new Set();
      for (let item of this.rows) {
        varSet.add(item.varName)
        varSet.add(item.varNameT)
      }
      return varSet
    }

    init(){
      let labels = ['<p>外观</p>','<p>性能</p>']
      let rate = null
      for(let label of labels){
        rate = new MulSortOptionClass();
        rate.htmlLabel = label
        this.rows.push(rate)
      }
    }
    addOption(index) {
      let option = new MulSortOptionClass();
      option.htmlLabel = "<p>选项" + (this.rows.length+1)+"</p>";
      if (index == -1) {//尾部插入
        this.rows.push(option)
      } else {//之后插入
        this.rows.splice(index + 1, 0, option);
      }
    }
    delOption(index){
      if (this.rows.length > 1) {
        this.rows.splice(index, 1)
      }
    }
    batchAddOptions(batchLabels){
      batchLabels.forEach(label => {
        let option = new MulSortOptionClass();
        option.htmlLabel = label;
        this.rows.push(option)
      })
    }


    /**
     * 上下移动选项
     * @param index
     * @param flag
     */
    moveOption(index,flag='pre'){
      let currOption = this.rows[index];
      if(flag==='pre'){//上移
        this.rows.splice(index, 1);//删除
        this.rows.splice(index - 1, 0, currOption);//再插入
      }else{
        this.rows.splice(index, 1);//删除
        this.rows.splice(index + 1, 0, currOption);//再插入
      }
    }
  }
  export default MulSortClass



