<template>
    <div :class="{anPc:isPc,anPhone:!isPc }">

      <div style="clear:both"></div>
      <an-view-text-ques v-if="survey.currPageIndex==-1"
                         :survey="survey" :ques="survey.startQues"></an-view-text-ques>
      <an-view-text-ques v-else-if="survey.currPageIndex==10000"
                         :survey="survey" :ques="survey.endQues"></an-view-text-ques>
      <an-view-ques v-else :survey="survey"></an-view-ques>
      <div style="text-align:center;margin-top:10px">
      <span style="font-weight: bold">小白问卷</span>&nbsp;提供技术支持
      </div>
      <div style="height:5px"></div>
    </div>

</template>

<script>

  import AnViewQues from '@/components/an/AnViewQues'
  import AnViewTextQues from '@/components/an/AnViewTextQues'
  export default {
    name: 'Dati',
    props: ['survey'],
    components: {
      AnViewQues,AnViewTextQues
    },
    data () {
      return {
        isPc:false
      };
    },
    created(){
      if(this.survey.device=='pc'){
        this.isPc = true
      }
    },
    mounted(){

    },
    methods: {

    },
    computed: {
    },
    watch: {
      //路由变化监听
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped lang="less">
  .anPc {
     width:910px;
     margin: auto;
     margin-top:20px;
     overflow-y: auto;
     overflow-x: auto;
   }
  .anPhone {
    width: 99%;
    margin: auto;
    overflow-y: auto;
    overflow-x: auto;
  }

</style>



