//每种题型的样式，Map数据结构，便于获取
let styleMap = new Map();
function quesStyles(quesType) {
  let style = styleMap.get(quesType)
  if (style != null) {
    return style;
  }
  for (let style of _quesStyles) {
    if (style.quesType == quesType) {
      style.styles.splice(0, 0, {code: '', label: '默认'})
      styleMap.set(quesType, style.styles)
      return style.styles;
    }
  }
  let defaultStyle = [{code: '', label: '默认'}]
  styleMap.set(quesType, defaultStyle)
  return defaultStyle
}

