//通过题型，得到图标
function quesType2Ico(quesType) {
  return quesType2IcoMap.get(quesType)
}
//通过题型，得到题型的名称
function quesType2Label(quesType) {
  for (let group of quesTypes2) {
    for (let quesTypeObj of group.childs) {
      if (quesTypeObj.quesType === quesType) {
        return quesTypeObj.label
      }
    }
  }
  return '题型没定义'
}

function createQues(quesJson = {quesType: 'Input'}, design = true) {
  let currQues = null;
  switch (quesJson.quesType) {
    case 'Radio':
      currQues = new RadioClass(design);
      break;
    case 'Checkbox':
      currQues = new CheckboxClass(design);
      break;
    case 'Input':
      currQues = new InputClass(design);
      break;
    case 'MulInput':
      currQues = new MulInputClass(design);
      break;
    case 'MatrixRadio':
      currQues = new MatrixRadioClass(design);
      break;
    case 'MatrixCheckbox':
      currQues = new MatrixCheckboxClass(design);
      break;
    case 'MatrixInput':
      currQues = new MatrixInputClass(design);
      break;
    case 'MatrixRate':
      currQues = new MatrixRateClass(design);
      break;
    case 'MatrixScroll':
      currQues = new MatrixScrollClass(design);
      break;
    case 'RadioRate':
      currQues = new RadioRateClass(design);
      break;
    case 'Desc':
      currQues = new DescClass(design);
      break;
    case 'File':
      currQues = new FileClass(design);
      break;
    case 'InputScroll':
      currQues = new InputScrollClass(design);
      break;
    case 'MulSort':
      currQues = new MulSortClass(design);
      break;
    case 'MatrixSelect':
      currQues = new MatrixSelectClass(design);
      break;
    case 'MatrixScale':
      currQues = new MatrixScaleClass(design);
      break;
    case 'RadioSelect':
      currQues = new RadioSelectClass(design);
      break;
    case 'RadioCascader':
      currQues = new RadioCascaderClass(design);
      break;
    case 'MulGoods':
      currQues = new MulGoodsClass(design);
      break;
    case 'RadioVote':
      currQues = new RadioVoteClass(design);
      break;
    case 'CheckboxVote':
      currQues = new CheckboxVoteClass(design);
      break;
    case 'Start':
      currQues = new StartClass(design);
      break;
    case 'End':
      currQues = new EndClass(design);
      break;
    default:
      currQues = new InputClass(design);
  }
  if (design) {
    let newQuesJson = JSON.parse(JSON.stringify(quesJson))
    for (let k of Object.keys(newQuesJson)) {
      currQues[k] = newQuesJson[k];
    }
  } else {
    for (let k of Object.keys(quesJson)) {
      currQues[k] = quesJson[k];
    }
  }

  return currQues;
}
/**
 * 浅复制克隆
 * @param quesObj
 * @returns {*}
 */
function cloneQues(quesObj = {quesType: 'Input'},design = true) {
  return createQues(quesObj,design);
}
function newQues(quesType = 'Input') {
  return createQues({quesType});
}
/**
 * 选项随机排序
 * @param options
 */
function optionRandom(options) {
  let noRandoms = [];// 不参加随机的选项
  for (let i = 0, j = 0; i < options.length; i++, j++) {
    if (options[i].noRandom) {
      noRandoms.push({index: j, item: options[i]});//临时储存不参加随机的选项
      options.splice(i, 1);//删除掉不参加随机的选项
      i--
    }
  }
  //随机排序
  options.sort(function (a, b) {
    return 0.5 - Math.random()
  });

  for (let noRandom of noRandoms) {
    options.splice(noRandom.index, 0, noRandom.item)
  }
}
