<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        <div>
          <template v-for="(item,index) in ques.rows">
            <i @click="clickRate(item,index)" @mouseenter="enterRate(index)"
               @mouseleave="currIndex = oldCurrIndex " class='iconfont'
               style="padding-right:15px" :title="Fun.html2Text(item.htmlLabel)"
               :style="rateStyle(index)" v-html="ques.icon">
            </i>
          </template>
        </div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultRadioRateView',
    components: {
      ViewQuesT
    },
    props: ['survey', 'ques'],
    data () {
      return {
        currIndex: -1,
        oldCurrIndex: -1,
      }
    },
    mounted(){
    },
    methods: {
      enterRate(index){
        this.oldCurrIndex = this.currIndex;
        this.currIndex = index;
      },
      rateStyle(index){
        let color = this.ques.unActiveColor;
        let iconSize = this.ques.iconSize + 'px';
        if (index <= this.currIndex) {
          color = this.ques.activeColor;
        }
        if (index == this.currIndex) {
          iconSize = (this.ques.iconSize + 1) + 'px';
        }
        let style = 'color:' + color + ';fontSize:' + iconSize;
        return style;

      },
      clickRate(item, index){
        this.currIndex = index;
        this.oldCurrIndex = index;
        this.survey.an[this.ques.varName] = item.val
      },
    },
    computed: {},
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  table td {
    padding: 3px 5px 3px 5px;
  }
</style>



