<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        <table :style="{width:ques.width}">
          <template v-for="(item,index) in ques.rows">
            <tr>
              <td>
                <div v-html="item.htmlGroup"></div>
              </td>
            </tr>
            <tr>
              <td>
                <div style="display:flex;align-items:center;">
                  <div :style="{width:ques.width1}" v-html="item.htmlLabel"></div>
                      <input v-model="survey.an[item.varName]" :type="Fun.inputType(item.rule)"
                             class="underline"  :style="{width:ques.width2}">
                    <span v-if="item.required" class="required"></span>
                    <div v-if="item.rightLabel.length>0" v-html="item.rightLabel"></div>
                </div>
              </td>
            </tr>
          </template>
        </table>
        <div style="height:3px"></div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultMulInputQuesView',
    components: {
      ViewQuesT
    },
    props: ['survey', 'ques'],
    data () {
      let an = {}
      for (let item of this.ques.rows) {
        an[item.varName] = undefined
      }
      return {}
    },

    mounted(){
    },
    methods: {},
    computed: {},
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>

  table td {
    padding: 3px 5px 3px 5px;
  }
</style>



