import {BaseQuesClass,BaseOptionClass} from './BaseClass'
import Fun from '../Fun'
let inc = 0 ;
  /**
   * 多选题选项
   */
  class MulInputOptionClass extends BaseOptionClass {
    constructor() {
      super();
      this.varName = 'M_'+Fun.getInc();
      this.fieldName = this.varName;
      this.htmlGroup = '';
      this.rightLabel = '';//右边标签
      this.noRandom = false;//选项固定，在使用随机显示选项才起作用
      this.required = false;//必答选项
      this.rule = '';//验证
    }
  }
  /**
   * 多选题
   */
  class MulInputClass extends BaseQuesClass {
    constructor(design=true) {
      super();
      this.quesType = 'MulInput';
      this.htmlTitle = '<p><strong>多行填空题</strong></p>';
      this.random = false;//选项随机
      this.min = 0;
      this.max = 0;
      this.width = '' ;
      this.width1 = '';//标签宽度

      this.pcWidth = '45%' ;
      this.pcWidth1 = '15%' ;
      this.pcWidth2 = '85%' ;

      this.phoneWidth = '100%' ;
      this.phoneWidth1 = '25%' ;
      this.phoneWidth2 = '75%' ;
      this.rows = []
      if(design){//设计问题
        this.init();
      }
    }

    /**
     * 验证答题
     * @param an
     * @returns {boolean}
     */
    anValid(an) {
      super.anValid(an);
      if (!this.required) {
        return true
      }
      let sum = 0;//填写的数量
      for (let item of this.rows) {
        if (Fun.isNull(an[item.varName])) {
          if (item.required) {
            this.errInfo = "标注*文本框必须填写！"
            return false;
          }
        } else {//文本框已有值
          if(!Fun.isNull(item.rule)){//有规则
            let tip = Fun.validRule(item.rule,an[item.varName])
            if(!Fun.isNull(tip)){
              this.errInfo = tip
              return false
            }
          }
          sum++
        }
      }
      if (sum == 0) {
        this.errInfo = "这道题必须回答哦！"
        return false
      }
      if (this.anHasOptionSum()) {//选项数量验证
        if (this.min > sum || this.max < sum) {
          this.errInfo = "填写个数不满足要求！"
          return false;
        }
      }
      this.errInfo = ""
      return true;
    }
    /**
     * 答题界面样式配置
     */
    anInit(){
      super.anInit();
      if (this.device == 'pc') {
        this.width = this.pcWidth ;
        this.width1 = this.pcWidth1 ;
        this.width2 = this.pcWidth2 ;
      }else{
        this.width = this.phoneWidth ;
        this.width1 = this.phoneWidth1 ;
        this.width2 = this.phoneWidth2 ;
      }
    }
    getVarNames(){
      let varSet = new Set();
      for (let item of this.rows) {
        varSet.add(item.varName)
      }
      return varSet
    }
    /**
     * 是否配置选项数量控制
     * @returns {boolean}
     */
    anHasOptionSum(){
      if (this.max != 0 && (this.min <= this.max)) {
        if (this.max <= this.rows.length) {
          return true;
        }
      }
      return false;
    }
    anOptionSumInfo(){
      let start = this.min > 0 ? '请填写' + this.min + '-' : '最多填写'
      return '[' + start + this.max + '项]'
    }

    /**
     * 答题时选项随机
     */
    anRandom(){
      super.anRandom();
      if (this.random) {
        Fun.optionRandom(this.rows)
      }
    }

    init(){
      let labels = ['<p>外观</p>','<p>性能</p>']
      let rate = null
      for(let label of labels){
        rate = new MulInputOptionClass();
        rate.htmlLabel = label
        this.rows.push(rate)
      }
    }
    addOption(index) {
      let option = new MulInputOptionClass();
      option.htmlLabel = "<p>选项" + (this.rows.length+1)+"</p>";
      if (index == -1) {//尾部插入
        this.rows.push(option)
      } else {//之后插入
        this.rows.splice(index + 1, 0, option);
      }
    }
    delOption(index){
      if (this.rows.length > 1) {
        this.rows.splice(index, 1)
      }
    }
    batchAddOptions(batchLabels){
      batchLabels.forEach(label => {
        let option = new MulInputOptionClass();
        option.htmlLabel = label;
        this.rows.push(option)
      })
    }


    /**
     * 上下移动选项
     * @param index
     * @param flag
     */
    moveOption(index,flag='pre'){
      let currOption = this.rows[index];
      if(flag==='pre'){//上移
        this.rows.splice(index, 1);//删除
        this.rows.splice(index - 1, 0, currOption);//再插入
      }else{
        this.rows.splice(index, 1);//删除
        this.rows.splice(index + 1, 0, currOption);//再插入
      }
    }
  }
  export default MulInputClass



