<template>
  <div style="display: inline;">
    <input @change="fileChange($event)" style="display:none" :id="id_"
           type="file" :accept="accept_"/>
    <el-button @click="browserFile">{{browserLabel_}}</el-button>
    <el-button v-if="!imme_" @click="uploadFile">{{uploadLabel_}}</el-button>
    <div v-if="preview_&&hasFile&&img" style="margin-top:5px;margin-left:3px">
      <a href="javascript:void(0)">
        <img :id="imgId" style="width:120px;height: 120px" class="img-thumbnail">
      </a>
    </div>
  </div>
</template>

<script>
  /**
   * id:文件表单元素id ；可以空,组件自己生成
   * name:服务器端定义的文件键名字；可以空 默认file
   * accept:浏览文件对话框，默认支持的文件类型；可以空，不过滤
   * size:上传文件大小控制 格式1024等于1k，可以空，默认200M
   * imme:立即上传，上传按钮隐藏
   * preview:图片预览，只有图片格式才支持预览，可以空，不预览
   * callError:是否调用端显示错误 ，发送errorTip事件 {code:0,tip:'内容'}，0:文件大小;1:文件没有扩展名;2:文件格式
   */
  export default {
    name: 'KkFile',
    props: ['id', 'name', 'accept', 'size', 'imme',
      'preview', 'browserLabel', 'uploadLabel', 'callError','other'],
    data () {
      return {
        id_: '',
        name_: 'file',
        accept_: '',
        size_: 100 * 1024 * 1024,
        imme_: false,
        preview_: false,
        callError_: false,
        browserLabel_: '浏览',
        uploadLabel_: '上传',
        other_: null,
        hasFile: false,
        imgId: 'p' + this.generateUUID(),
        img: false,
        staticAccept: new Map(),
      }
    },
    mounted(){
      this.init();
      this.initStaticAccept();
      this.initAccept();
    },
    methods: {
      /**
       * 默认值初始化
       */
      init(){
        if (this.id === undefined) {
          this.id_ = 'f' + this.generateUUID()
        } else {
          this.id_ = this.id;
        }
        if (this.name != undefined) {
          this.name_ = this.name;
        }
        if (this.size != undefined) {
          this.size_ = this.size;
        }
        if (this.callError != undefined) {
          this.callError_ = this.callError;
        }
        if (this.imme != undefined) {
          this.imme_ = this.imme;
        }
        if (this.preview != undefined) {
          this.preview_ = this.preview;
        }
        if (this.browserLabel != undefined) {
          this.browserLabel_ = this.browserLabel;
        }
        if (this.uploadLabel != undefined) {
          this.uploadLabel_ = this.uploadLabel;
        }
        if (this.other != undefined) {
          this.other_ = this.other;
        }
      },
      /**
       * 初始化文件类型过滤
       */
      initAccept(){
        if (this.accept != undefined) {
          let _accept = '';
          this.accept.split(",").forEach(val => {
            if (this.staticAccept.get(val) != null) {
              _accept += this.staticAccept.get(val) + ",";
            }
          })
          this.accept_ = _accept;
        }
      },
      /**
       * 选择本地文件，触发改变事件
       */
      fileChange(event){
        let file = event.target.files[0];
        if (file == undefined) {
          //console.log("取消选择文件")
          return
        }
        this.hasFile = true;
        this.previewImg(file);
        let fileName = file.name;
        let fileType = file.type;
        let fileSize = this.fileSizeConvert(file.size);
        let shortName = fileName;//去掉扩展名后的文件名称
        let extPos = fileName.indexOf(".");
        if (extPos > -1) {
          shortName = fileName.substring(0, extPos);
        }
        let callObj = {
          fileName: fileName, shortName: shortName,
          fileType: fileType, fileSize: fileSize
        }
        if(this.other_!=null){
          callObj.other = this.other_;
        }
        //向父组件发送选择文件改变消息
        this.$emit('fileChange', callObj);
        //立即上传
        if (this.imme_) {
          this.uploadFile();
        }
      },
      /**
       * 图片预览
       */
      previewImg(file){
        if (this.preview_ && this.hasFile) {
          this.img = true;
          if (!/^image/.test(file.type)) {
            //console.log("仅支持图片预览！");
            this.img = false;
            return
          }
          let that = this;
          let reader = new FileReader();
          reader.readAsDataURL(file);
          reader.onload = function (e) {
            let _img = document.querySelector("#" + that.imgId);
            _img.src = this.result;
          }
        }
      },
      /**
       * 打开本地文件浏览对话框
       */
      browserFile(){
        document.getElementById(this.id_).click();
      },
      /**
       * 上传文件
       */
      uploadFile(){
        let file = document.getElementById(this.id_).files[0];
        let fileName = file.name;
        let fileSize = file.size;
        //上传文件类型控制
        let success = this.fileExtNameValid(fileName);
        if (!success) {
          return;
        }
        //上传文件大小控制
        success = this.fileSizeValid(fileSize);
        if (!success) {
          return;
        }
        //向父组件发送消息
        let formdata = new FormData();
        formdata.append(this.name_, file);
        this.$emit('uploadFile', formdata);
      },
      /**
       * 上传文件大小控制验证
       */
      fileSizeValid(fileSize){
        if (fileSize > this.size_) {
          fileSize = this.fileSizeConvert(this.size_);
          let errorInfo = '最大允许上传文件大小：' + fileSize;
          if (this.callError_) {//客户端控制错误提示
            this.$emit('errorTip', {code: 0, tip: errorInfo});
          } else {
            this.$alert(errorInfo, '信息提示', {confirmButtonText: '关闭', closeOnClickModal: true});
          }
          return false;
        }
        return true;
      },
      /**
       * 上传文件类型控制验证
       */
      fileExtNameValid(fileName){
        let extPos = fileName.lastIndexOf(".");
        if (this.accept != undefined && this.accept != '') {
          if (extPos > 0) {
            let extName = fileName.substring(extPos + 1);
            extPos = this.accept.split(",").findIndex(val => {
              return extName == val;
            })
          }
          if (extPos < 0) {
            let errorInfo = '请上传：' + this.accept;
            if (this.callError_) {//客户端控制错误提示
              this.$emit('errorTip', {code: 2, tip: errorInfo});
            } else {
              this.$alert(errorInfo, '信息提示', {confirmButtonText: '关闭', closeOnClickModal: true});
            }
            return false;
          }
        }
        return true;
      },
      /**
       * 字节大小转化为KB或MB
       * @param fileSize
       * @returns {*}
       */
      fileSizeConvert(fileSize){
        if (fileSize > 1024 * 1024) {
          fileSize = (Math.round(fileSize * 100 / (1024 * 1024)) / 100).toString() + 'MB';
        } else {
          fileSize = (Math.round(fileSize * 100 / 1024) / 100).toString() + 'KB';
        }
        return fileSize;
      },
      /**
       * 唯一ID 碰撞率不及1/2^^122
       * @returns {string}
       */
      generateUUID() {
        let d = new Date().getTime();
        let uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
          let r = (d + Math.random() * 16) % 16 | 0;
          d = Math.floor(d / 16);
          return (c == 'x' ? r : (r & 0x3 | 0x8)).toString(16);
        });
        return uuid;
      },
      /**
       * 初始化静态mime类型列表
       */
      initStaticAccept(){

        this.staticAccept.set("bmp", 'image/bmp');
        this.staticAccept.set("gif", 'image/gif');
        this.staticAccept.set("jpeg", 'image/jpeg');
        this.staticAccept.set("png", 'image/png');
        this.staticAccept.set("txt", 'text/plain');
        this.staticAccept.set("csv", 'text/csv');
        this.staticAccept.set("doc", 'application/msword');
        this.staticAccept.set("docx", 'application/vnd.openxmlformats-officedocument.wordprocessingml.document');
        this.staticAccept.set("ppt", 'application/vnd.ms-powerpoint#请上传PPT文件！');
        this.staticAccept.set("pptx", 'application/vnd.openxmlformats-officedocument.presentationml.presentation');
        this.staticAccept.set("xls", 'application/vnd.ms-excel#请上传EXCEL文件！');
        this.staticAccept.set("xlsx", 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet');
        this.staticAccept.set("rar", 'rar');
        this.staticAccept.set("zip", 'zip');
        this.staticAccept.set("pdf", 'pdf');
        this.staticAccept.set("gzip", 'gzip');


      },

      watchRouteInit(val, oldval){

      }
    },
    watch: {
      '$route': 'watchRouteInit'
    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



