<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        {{horChange}}
        <table :style="{width:ques.width}">
          <template v-for="(rowArr,rowIndex) in tableArr">
            <tr>
              <td v-for="(item,index) in rowArr">
                <label class="activerow" style="padding:0 5px;display:flex;align-items:center;">
                  <div :style="{width:ques.width1}"
                       style="display:flex;align-items:center;">
                    <el-radio v-model="survey.an[ques.varName]" :label="item.val">&nbsp;</el-radio>
                    <div v-html="item.htmlLabel"></div>
                  </div>
                  <div v-if="ques.fillFlag&&survey.an[ques.varName]==item.val"
                       style="width:100%;display: flex;">
                    <input v-model="survey.an[ques.varNameT]" style="width:100%"
                           class="underline" type="text">
                    <span v-if="ques.fillRequired" class="required"></span>
                  </div>
                </label>
              </td>
            </tr>
          </template>
        </table>
        <div style="height:3px"></div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultRadioView',
    components: {
      ViewQuesT,
    },
    props: ['survey', 'ques'],
    data () {
      return {
        colSize:1,//每行几列
        tableArr:new Array(),
      }
    },
    created(){
      //this.changeLayout()
    },
    mounted(){
    },
    methods: {
          changeLayout(){
            this.tableArr = new Array()
            this.colSize = Number.parseInt(this.ques.horCol);//每行几列
            this.ques.rows.forEach((item,index) => {
              let rowIndex = Math.floor(index/this.colSize);//向下取整
              let rowArr = null
              if(this.tableArr.length>rowIndex){
                rowArr = this.tableArr[rowIndex];
              }else{
                rowArr = new Array()
                this.tableArr.push(rowArr)
              }
              rowArr.push(item)
            });
          }
    },
    computed: {
      horChange: function (newVal, oldVal) {
        this.changeLayout();
      }
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  table td {
    padding: 3px 0;
  }
</style>



