import {BaseOptionClass, BaseQuesClass} from "./BaseClass";
import Fun from "../Fun";

/**
 * 选项
 */
class MatrixInputRowOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.varName = 'F_'+Fun.getInc();
    this.fieldName =  this.varName;
    this.htmlGroup = '';
    this.rule = '';//验证
    this.noRandom = false;//选项固定，在使用随机显示选项才起作用
  }
}
class MatrixInputColOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.varName = 'F_'+Fun.getInc();
    this.fieldName = this.varName;
    this.noRandom = false;//选项固定，在使用随机显示选项才起作用
  }
}
/**
 * 矩阵填空题
 */
class MatrixInputClass extends BaseQuesClass {
  constructor(design=true) {
    super();
    this.quesType = 'MatrixInput';
    this.htmlTitle = '<p><strong>矩阵填空题</strong></p>';
    this.random = false;//选项随机
    this.optionSumShow = true ;//是否选项数限制提示，
    this.min = 0;
    this.max = 0;
    this.width = '' ;
    this.pcWidth = '45%' ;
    this.phoneWidth = '100%' ;
    this.rows = [];
    this.cols = [];
    if(design){//设计问题
      this.init();
    }
  }

  /**
   * 是否配置选项数量控制
   * @returns {boolean}
   */
  anHasOptionSum(){
    if (this.max != 0 && (this.min <= this.max)) {
      if (this.max <= this.cols.length) {
        return true;
      }
    }
    return false;
  }
  anOptionSumInfo(){
    let start = this.min > 0 ? '请填写' + this.min+ '-' : '最多填写'
    return '[每行' + start + this.max + '项]'
  }
  /**
   * 验证答题
   * @param an
   * @returns {boolean}
   */
  anValid(an) {
    if (!this.required) {
      return true
    }
    let sums = [];//每一行选择的数量
    for (let i = 0; i < this.rows.length; i++) {
      let row = this.rows[i]
      sums[i] = 0;
      for (let col of this.cols) {
        if (!Fun.isNull(an[row.varName + col.varName])) {
          sums[i]++;
        }
      }
    }
    for (let sum of sums) {
      if (sum == 0) {
        this.errInfo = "每一行都需要回答哦！"
        return false
      }
    }

    if (this.anHasOptionSum()) {//选项数量验证
      for (let sum of sums) {
        if (this.min > sum || this.max < sum) {
          this.errInfo = "填写个数不满足要求！"
          return false;
        }
      }
    }
    this.errInfo = ""
    return true
  }
  /**
   * 答题界面样式配置
   */
  anInit(){
    super.anInit();
    if(this.device=='pc'){
      this.width = this.pcWidth ;
    }else{
      this.width = this.phoneWidth ;
    }
  }
  /**
   * 答题时选项随机
   */
  anRandom(){
    super.anRandom();
    if (this.random) {
      Fun.optionRandom(this.rows)
      Fun.optionRandom(this.cols)
    }
  }
  init(){
    let labels = ['<p>功能</p>','<p>外观</p>']
    let rate = null
    for(let label of labels){
      rate = new MatrixInputRowOptionClass();
      rate.htmlLabel = label
      this.rows.push(rate)
    }

    labels = ['<p>华为</p>','<p>小米</p>','<p>苹果</p>']
    for(let label of labels){
      rate = new MatrixInputColOptionClass();
      rate.htmlLabel = label
      this.cols.push(rate)
    }
  }

  getVarNames(){
    let varSet = new Set();
    for (let row of this.rows) {
      for (let col of this.cols) {
        varSet.add(row.varName+col.varName)
      }
    }
    return varSet
  }

  addOption(index,flag) {
    if(flag=='row'){
      let option = new MatrixInputRowOptionClass();
      option.htmlLabel = "<p>选项" + (this.rows.length + 1)+"</p>";
      if (index == -1) {//尾部插入
        this.rows.push(option)
      } else {//之后插入
        this.rows.splice(index + 1, 0, option);
      }
    }else{
      let option = new MatrixInputColOptionClass();
      option.htmlLabel = "<p>选项" + (this.cols.length + 1)+"</p>";
      if (index == -1) {//尾部插入
        this.cols.push(option)
      } else {//之后插入
        this.cols.splice(index + 1, 0, option);
      }
    }


  }

  delOption(index,flag) {
    if(flag=='row'){
      if (this.rows.length > 1) {
        this.rows.splice(index, 1)
      }
    }else{
      if (this.cols.length > 1) {
        this.cols.splice(index, 1)
      }
    }

  }

  batchAddOptions(batchLabels,flag) {
    if(flag=='row'){
      batchLabels.forEach(label => {
        let option = new MatrixInputRowOptionClass();
        option.htmlLabel = label;
        this.rows.push(option)
      })
    }else{
      batchLabels.forEach(label => {
        let option = new MatrixInputColOptionClass();
        option.htmlLabel = label;
        this.cols.push(option)
      })
    }

  }

  /**
   * 获取批量显示的选项标签,用于打开批量增加选项对话框的默认值
   */
  getBatchLabels(flag) {
    if(flag=='row'){
      let htmlLabels = []
      this.rows.forEach((option) => {
        let htmlLabel = option.htmlLabel;
        if (!Fun.isNull(htmlLabel)) {
          htmlLabels.push(htmlLabel)
        }
      })
      return htmlLabels;
    }else{
      let htmlLabels = []
      this.cols.forEach((option) => {
        let htmlLabel = option.htmlLabel;
        if (!Fun.isNull(htmlLabel)) {
          htmlLabels.push(htmlLabel)
        }
      })
      return htmlLabels;
    }

  }

  /**
   * 上下移动选项
   * @param index
   * @param flag
   */
  moveOption(index,optionFlag, flag = 'pre') {
    if(optionFlag=='row'){
      let currOption = this.rows[index];
      if (flag === 'pre') {//上移
        this.rows.splice(index, 1);//删除
        this.rows.splice(index - 1, 0, currOption);//再插入
      } else {
        this.rows.splice(index, 1);//删除
        this.rows.splice(index + 1, 0, currOption);//再插入
      }
    }else{
      let currOption = this.cols[index];
      if (flag === 'pre') {//上移
        this.cols.splice(index, 1);//删除
        this.cols.splice(index - 1, 0, currOption);//再插入
      } else {
        this.cols.splice(index, 1);//删除
        this.cols.splice(index + 1, 0, currOption);//再插入
      }
    }

  }
}
export default MatrixInputClass




