<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        <table :style="{width:ques.width}">
          <tr>
            <td></td>
            <td>
              <div v-html="ques.leftTitle" style="float:left;">
              </div>
              <div v-html="ques.rightTitle" style="float:right;">
              </div>
            </td>
          </tr>
          <template v-for="(item,index) in ques.rows">
            <tr>
              <td colspan="2">
                <div v-html="item.htmlGroup"></div>
              </td>
            </tr>
            <tr>
              <td :style="{width:ques.width1}">
                <div v-html="item.htmlLabel"></div>
              </td>
              <td>
                <el-slider v-model="survey.an[item.varName]" :min="ques.minVal" :max="ques.maxVal"></el-slider>
              </td>
            </tr>
          </template>
        </table>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultMatrixScrollView',
    components: {
      ViewQuesT
    },
    props: ['survey', 'ques'],
    data () {
      let an = {}
      for (let item of this.ques.rows) {
        an[item.varName] = undefined
      }
      return {
      }
    },

    mounted(){},
    methods: {

    },
    computed: { },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  table td {
    padding: 3px 5px 3px 5px;
  }

</style>



