import {BaseOptionClass, BaseQuesClass} from "./BaseClass";
import Fun from "../Fun";

let inc = 0;
/**
 * 单选题选项
 */
class RadioCascaderOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.val = Fun.getValInc();
    this.default = false;//默认选中
  }
}
/**
 * 单选题
 */
class RadioCascaderClass extends BaseQuesClass {
  constructor(design = true) {
    super();
    this.quesType = 'RadioCascader';
    this.htmlTitle = '<p><strong>联动题</strong></p>';
    this.varName = 'O_'+Fun.getInc();
    this.fieldName = this.varName;
    this.width = '';
    this.pcWidth = '30%';
    this.phoneWidth = '100%';
    this.rows = [];
    if (design) {//设计问题
      this.init();
    }
  }

  /**
   * 答题界面样式配置
   */
  anInit() {
    super.anInit();
    if (this.device == 'pc') {
      this.width = this.pcWidth;
    } else {
      this.width = this.phoneWidth;
    }
  }

  /**
   * 验证答题
   * @param an
   * @returns {boolean}
   */
  anValid(an) {
    super.anValid(an);
    if (!this.required) {
      return true
    }
    if (Fun.isNull(an[this.varName])) {
      this.errInfo = "这道题必须回答哦！"
      return false
    }
    this.errInfo = ""
    return true
  }

  init() {
    let labels = ['北京', '上海']
    let rate = null
    for (let label of labels) {
      rate = new RadioCascaderOptionClass();
      rate.htmlLabel = label
      this.rows.push(rate)
    }
  }

  getVarNames(){
    let varSet = new Set();
    varSet.add(this.varName)
    return varSet
  }

  addOption(index) {
    let option = new RadioCascaderOptionClass();
    option.htmlLabel = "选项" + (this.rows.length + 1);
    option.val = 'valR' + (++inc);
    if (index == -1) {//尾部插入
      this.rows.push(option)
    } else {//之后插入
      this.rows.splice(index + 1, 0, option);
    }
  }

  delOption(index) {
    if (this.rows.length > 1) {
      this.rows.splice(index, 1)
    }
  }

  batchAddOptions(batchLabels) {
    batchLabels.forEach(label => {
      let option = new RadioCascaderOptionClass();
      option.htmlLabel = label;
      this.rows.push(option)
    })
  }

  /**
   * 获取批量显示的选项标签,用于打开批量增加选项对话框的默认值
   */
  getBatchLabels() {
    let htmlLabels = []
    this.rows.forEach((option) => {
      let htmlLabel = option.htmlLabel;
      if (!Fun.isNull(htmlLabel)) {
        htmlLabels.push(htmlLabel)
      }
    })
    return htmlLabels;
  }

  /**
   * 上下移动选项
   * @param index
   * @param flag
   */
  moveOption(index, flag = 'pre') {
    let currOption = this.rows[index];
    if (flag === 'pre') {//上移
      this.rows.splice(index, 1);//删除
      this.rows.splice(index - 1, 0, currOption);//再插入
    } else {
      this.rows.splice(index, 1);//删除
      this.rows.splice(index + 1, 0, currOption);//再插入
    }
  }
}
export default RadioCascaderClass




