<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        {{horChange}}
        <table :style="{width:ques.width}">
          <template v-for="(rowArr,rowIndex) in tableArr">
            <tr>
              <td v-for="(item,index) in rowArr">
                <label class="activerow" style="width:99%;padding: 0 5px">
                  <div >
                    <div style="display:flex;align-items:center;">
                      <el-radio @change="calcRate(item.val)" v-model="survey.an[ques.varName]" :label="item.val">
                        &nbsp;
                      </el-radio>
                      <div v-html="item.htmlLabel"></div>
                    </div>
                  </div>
                </label>
                <div v-if="show" style="margin-left:3px">
                  <div style="display:flex;align-items: center">
                    <div style="width:85%">
                    <el-progress :stroke-width="18" :text-inside="true" :percentage="voteRes[item.val].rate"></el-progress>
                    </div>
                    <div>
                      <span style="color:#53a4f4">{{voteRes[item.val].count}}票</span>
                    </div>
                  </div>
                </div>
              </td>
            </tr>
          </template>
        </table>
        <div style="height:3px"></div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'TextRadioVoteView',
    components: {
      ViewQuesT,
    },
    props: ['survey', 'ques'],
    data () {
      return {
        colSize: 1,//每行几列
        tableArr: new Array(),
        pager: {
          currPage: 0,
          total: 0,
          logic: true,
          pageSize: 10,
          sorts: [{col: 'created', op: 'desc'}],
          filters: [],
        },
        show: false,
        voteRes: {},
        origVoteRes: {},
        total: 1,
      }
    },
    created(){
      //this.changeLayout()
    },
    mounted(){
      if (this.survey.model == 1) {//答题测试模式
        this.fetchData();
      }
    },
    methods: {
      calcRate(currVal){
        if (this.survey.model == 1&&this.ques.voteShow) {
          this.show = true
          this.voteRes = JSON.parse(JSON.stringify(this.origVoteRes));
          for (let key in this.voteRes) {
            if (currVal == key) {
              this.voteRes[key].count = this.voteRes[key].count + 1
              break;
            }
          }
          for (let key in this.voteRes) {
            this.voteRes[key].rate = ((this.voteRes[key].count / (this.total + 1)) * 100).toFixed(2)*1
          }
        }
      },
      fetchData(){
        let _reqObj = {
          url: 'pub/radioRes',
          data: {pager: this.pager, quesKey: this.ques._id, surveyId: this.survey.surveyId},
          method: 'post',
        }
        return this.SERVICE2(_reqObj).then(res => {
          this.total = res.data.res.total
          for (let row of this.ques.rows) {//初始化每个选项的投票值
            let vote = {rate: 0, count: 0}
            this.origVoteRes[row.val] = vote
            for (let key in res.data.res.voteRes) {
              if (row.val == key) {
                vote.count = res.data.res.voteRes[key]
              }
            }
          }

        })
      },
      changeLayout(){
        this.tableArr = new Array()
        this.colSize = Number.parseInt(this.ques.horCol);//每行几列
        this.ques.rows.forEach((item, index) => {
          let rowIndex = Math.floor(index / this.colSize);//向下取整
          let rowArr = null
          if (this.tableArr.length > rowIndex) {
            rowArr = this.tableArr[rowIndex];
          } else {
            rowArr = new Array()
            this.tableArr.push(rowArr)
          }
          rowArr.push(item)
        });
      }
    },
    computed: {
      horChange: function (newVal, oldVal) {
        this.changeLayout();
      }
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  table td {
    padding: 3px 0;
  }
</style>



