import {BaseQuesClass,BaseOptionClass} from './BaseClass'
import Fun from '../Fun'

  /**
   * 单选题选项
   */
  class RadioRateOptionClass extends BaseOptionClass {
    constructor() {
      super();
      this.val = Fun.getValInc();
      this.default = false;//默认选中
    }
  }
  /**
   * 单选题
   */
  class RadioRateClass extends BaseQuesClass {
    constructor(design=true) {
      super();
      this.quesType = 'RadioRate';
      this.htmlTitle = '<p><strong>量表题</strong></p>';
      this.varName = 'Q_'+Fun.getInc();
      this.fieldName =  this.varName;

      this.width = '';
      this.pcWidth = '45%';
      this.phoneWidth = '100%';

      this.unActiveColor = '#c6d1de';
      this.pcUnActiveColor = '#c6d1de';
      this.phoneUnActiveColor = '#c6d1de';

      this.activeColor = '#f7ba2a';
      this.pcActiveColor = '#f7ba2a';
      this.phoneActiveColor = '#f7ba2a';

      this.icon = '&#xe6d8;';
      this.pcIcon = '&#xe6d8;';
      this.phoneIcon = '&#xe6d8;';

      this.iconSize = 20;
      this.pcIconSize = 20;
      this.phoneIconSize = 20;
      this.rows = []
      if(design){//设计问题
        this.init();
      }
    }
    /**
     * 验证答题
     * @param an
     * @returns {boolean}
     */
    anValid(an) {
     // console.log(JSON.stringify(an))
      if (this.required) {
        if (Fun.isNull(an[this.varName])) {
          this.errInfo = "这道题必须回答哦！"
          return false
        }
      }
      this.errInfo = ""
      return true
    }
    /**
     * 单选题默认值
     * @param an
     */
    anDefaultVal(VUE,an){
      super.anDefaultVal(an);
      for (let item of this.rows) {
        if (item.default) {
          an[this.varName] = item.val
          break;
        }
      }
    }
    getVarNames(){
      let varSet = new Set();
      varSet.add(this.varName)
      return varSet
    }
    /**
     * 答题界面样式配置
     */
    anInit(){
      super.anInit();
      if (this.device == 'pc') {
        this.width = this.pcWidth ;
        this.unActiveColor = this.pcUnActiveColor ;
        this.activeColor = this.pcActiveColor ;
        this.icon = this.pcIcon ;
        this.iconSize = this.pcIconSize ;
      }else{
        this.width = this.phoneWidth ;
        this.unActiveColor = this.phoneUnActiveColor ;
        this.activeColor = this.phoneActiveColor ;
        this.icon = this.phoneIcon ;
        this.iconSize = this.phoneIconSize ;
      }
    }
    init(){
      let labels = ['<p>不满意</p>','<p>2</p>','<p>3</p>','<p>很满意</p>']
      let rate = null
      for(let i=0;i<labels.length;i++){
        rate = new RadioRateOptionClass();
        rate.val = ""+(i+1)
        rate.htmlLabel = labels[i]
        this.rows.push(rate)
      }
    }
    addOption(index) {
      let option = new RadioRateOptionClass();
      option.htmlLabel = "<p>" + (this.rows.length+1)+"</p>";
      option.val = ''+(this.rows.length+1);
      if (index == -1) {//尾部插入
        this.rows.push(option)
      } else {//之后插入
        this.rows.splice(index + 1, 0, option);
      }
    }
    delOption(index){
      if (this.rows.length > 1) {
        this.rows.splice(index, 1)
      }
    }
    batchAddOptions(batchLabels){
      batchLabels.forEach(label => {
        let option = new RadioRateOptionClass();
        option.htmlLabel = label;
        this.rows.push(option)
      })
    }

    /**
     * 获取批量显示的选项标签,用于打开批量增加选项对话框的默认值
     */
    getBatchLabels(){
      let htmlLabels = []
      this.rows.forEach((option) => {
        let htmlLabel = option.htmlLabel;
        if (!Fun.isNull(htmlLabel)) {
          htmlLabels.push(htmlLabel)
        }
      })
      return htmlLabels ;
    }

    /**
     * 上下移动选项
     * @param index
     * @param flag
     */
    moveOption(index,flag='pre'){
      let currOption = this.rows[index];
      if(flag==='pre'){//上移
        this.rows.splice(index, 1);//删除
        this.rows.splice(index - 1, 0, currOption);//再插入
      }else{
        this.rows.splice(index, 1);//删除
        this.rows.splice(index + 1, 0, currOption);//再插入
      }
    }
  }
  export default RadioRateClass




