<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        <div style="height:5px"></div>
        <el-select v-model="survey.an[ques.varName]"
                   :style="{width:ques.width}" placeholder="请选择">
          <template v-for="(group,index) in getSelect">
            <el-option-group v-if="group.childs" class="radioSelect"
                             :key="group.code"
                             :label="group.label">
              <el-option
                v-for="item in group.childs"
                :key="item.code"
                :label="item.label"
                :value="item.code">
              </el-option>
            </el-option-group>
            <el-option v-else :label="group.label" :value="group.code">
            </el-option>
          </template>
        </el-select>
        <div style="height:5px"></div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultRadioSelectView',
    components: {
      ViewQuesT,
    },
    props: ['survey', 'ques'],
    data () {

      return {}
    },
    mounted(){
    },
    methods: {},
    computed: {
      getSelect(){
        let groups = []
        let group = null;
        let index = 1;
        for (let item of this.ques.rows) {
          if (item.htmlGroup.length > 0) {
            group = {code: index, label: item.htmlGroup, childs: []};
            group.childs.push({code: item.val, label: item.htmlLabel})
            groups.push(group)
            index++;
          } else {
            if (group != null) {
              group.childs.push({code: item.val, label: item.htmlLabel})
            } else {
              groups.push({code: item.val, label: item.htmlLabel})
            }
          }
        }
        return groups;
      },
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .radioSelect.el-select-group__wrap li.el-select-group__title {
    font-size: 14px;
    font-weight: bold;
  }

  .radioSelect.el-select-group__wrap li ul.el-select-group li {
    margin-left: 6px;
  }

</style>
<style scoped>

</style>



