<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        <table :style="{width:ques.width}">
          <tr>
            <td></td>
            <td v-for="(itemCol,indexCol) in ques.cols" style="text-align:center">
              <div v-html="itemCol.val"></div>
            </td>
          </tr>
          <template v-for="(itemRow,indexRow) in ques.rows">
            <tr>
              <td colspan="3">
                <div v-html="itemRow.htmlGroup"></div>
              </td>
            </tr>
            <tr>
              <td>
                <div style="min-width:45px" v-html="itemRow.htmlLabel"></div>
              </td>
              <template v-for="(itemCol,indexCol) in ques.cols">
                <td style="text-align:center">
                  &nbsp;&nbsp;&nbsp;<el-radio v-model="survey.an[itemRow.varName]" :label="itemCol.val">&nbsp;</el-radio>
                </td>
              </template>
            </tr>
          </template>
        </table>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultMatrixRateView',
    components: {
      ViewQuesT
    },
    props: ['survey', 'ques'],
    data () {
      return {}
    },
    mounted(){
    },
    methods: {},
    computed: {
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  table td {
    padding: 3px 5px 3px 5px;
  }

</style>



