  import {BaseQuesClass} from './BaseClass'
  import Fun from '../Fun'
  let inc = 0 ;
  /**
   * 附件题
   */
  class FileClass extends BaseQuesClass {
    constructor(design=true) {
      super();
      this.quesType = 'File';
      this.htmlTitle = '<p><strong>附件题</strong></p>';
      this.varName = 'B_'+Fun.getInc();
      this.fieldName = this.varName;
      this.width = '';
      this.pcWidth = '45%';
      this.phoneWidth = '100%';

      this.maxSize = 1;//文件大小限制1M
      this.fileTypes = []
      this.typesShow = true ;//显示上传文件类型提示
    }
    /**
     * 验证答题
     * @param an
     * @returns {boolean}
     */
    anValid(an) {
      if (this.required) {
        if (Fun.isNull(an[this.varName])) {
          this.errInfo = "这道题必须回答哦！"
          return false
        }
      }
      this.errInfo = ""
      return true
    }
    getVarNames(){
      let varSet = new Set();
      varSet.add(this.varName)
      return varSet
    }
    /**
     * 答题界面样式配置
     */
    anInit(){
      super.anInit();
      if (this.device == 'pc') {
        this.width = this.pcWidth ;
      }else{
        this.width = this.phoneWidth ;
      }
    }
  }
  export default FileClass




