<template>
  <div>
    <view-ques-t-t :survey="survey" :ques="ques">
      <div slot="textQues">
        <!--iframe v-if="ques.rows[itemIndex].urlShow"
                :src="ques.rows[itemIndex].url"
                style="width: 100%;border:0;"/-->
        <div v-html="ques.rows[itemIndex].htmlLabel"></div>
      </div>
    </view-ques-t-t>
  </div>
</template>

<script>
  import ViewQuesTT from '../../ViewQuesTT'
    export default {
    name: 'DefaultStartView',
    components: {
      ViewQuesTT,
    },
    props: ['survey', 'ques'],
    data () {
      return {
        itemIndex: 0
      }
    },

    mounted(){
      let endAn = this.survey.an['end'];
      if (endAn !== undefined) {
        let index = 0;
        for (let item of this.ques.rows) {
          if (endAn == item.val) {
            this.itemIndex = index;
            break;
          }
          index++;
        }
      }
    },
    methods: {},
    computed: {},
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .ques div.startBt button p {
    margin: 0 3px;
  }
</style>
<style scoped>


</style>



