<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        <table :style="{width:ques.width}">
          <tr>
            <td>
              <div v-html="ques.rows[0].htmlLabel"></div>
            </td>
            <td v-for="(item,index) in ques.rows">
              <el-radio v-model="survey.an[ques.varName]" :label="item.val">&nbsp;{{item.val}}</el-radio>
            </td>
            <td>
              <div v-html="ques.rows[ques.rows.length - 1].htmlLabel"></div>
            </td>
          </tr>
        </table>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultRadioRateView',
    components: {
      ViewQuesT
    },
    props: ['survey', 'ques'],
    data () {
      return {}
    },
    mounted(){
    },
    methods: {},
    computed: {
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  table td {
    padding: 3px 5px 3px 5px;
  }
</style>



