<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        <table :style="{width:ques.width}">
          <tr>
            <td>
              <div v-html="ques.leftTitle" style="float:left;">
              </div>
              <div v-html="ques.rightTitle" style="float:right;">
              </div>
            </td>
          </tr>
          <tr>
            <td style="padding-left:10px">
              <el-slider v-model="survey.an[ques.varName]" :min="ques.minVal" :max="ques.maxVal"></el-slider>
            </td>
          </tr>
        </table>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultInputScrollView',
    components: {
      ViewQuesT
    },
    props: ['survey', 'ques'],
    data () {
      return {
      }
    },
    mounted(){},
    methods: { },
    computed: {},
    watch: {}
  }

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  table td {
    padding: 2px 5px 2px 5px;
  }
</style>



