import Vue from "vue";
import axios from "axios";
import {Message} from "element-ui";
//require('es6-promise').polyfill();

axios.defaults.headers.post['Content-Type'] = 'application/json'
axios.defaults.baseURL = BASE_URL

Vue.prototype.$http = axios;

// http response 拦截器
axios.interceptors.response.use(
  response => {
    let _res = response.data
    if (_res.status === 'O') {//session过期
      parent.location.href = "login.html";
    } else if (_res.status === 'F') {
      Message({
        message: _res.msg,
        duration:5000,
        type: 'error',
        showClose: true,
      });
      return Promise.reject(_res); // 返回接口返回的错误信息
    }
    return response;
  },
  error => {
    //console.log(error.response.statusText)
    Message({
      message: error.response.statusText,
      type: 'error',
      showClose: true,
    });
    return Promise.reject(error); // 返回接口返回的错误信息
  });

function axiosReq(_type, _reqObj, successCallBack) {
  let _url = _reqObj.url;
  let _params = _reqObj.params;
  let _show = _reqObj.show;
  if (_show === null || _show == undefined) {
    _show = false;
  }
  let _axios = null;
  if (_type === 'GET') {
    if (_params == null || _params == undefined) {
      _axios = axios.get(_url)
    } else {
      _axios = axios.get(_url, {params: _params})
    }
  } else {
    if (_params == null || _params == undefined) {
      _axios = axios.post(_url)
    } else {
      _axios = axios.post(_url, _params)
    }
  }
  _axios.then(res => {
    // if (res.data.status != 'Y') {
    //   if (_show) {
    //     Message({
    //       message: res.data.msg,
    //       type: 'warning'
    //     });
    //   }
    // }
    successCallBack(res);
  });
  return _axios;
}
export default{
  get: function (_reqObj, successCallBack) {/**/
    return axiosReq('GET', _reqObj, successCallBack);
  },
  post: function (_reqObj, successCallBack) {/**/
    return axiosReq('POST', _reqObj, successCallBack);
  },
}



