import service from "../config/service.js";
import SERVICE2 from "../config/service2.js";
import Storage from "./Storage.js";
import QuesUtils from "./QuesUtils.js";
import EL from "./EL.js";
import Fun from "./Fun.js";
import PageClass from "./PageClass.js";
import {Message} from "element-ui";
/**
 * 问卷整体结构
 */
class SurveyClass {
  constructor() {
    this.surveyId = '';
    this.model = 0;//0编辑问卷模式，1答题模式
    this.state = 0;
    this.lib = 'N';
    this.an = {};
    this.backPath='';//问卷设计页，返回到那个主页面
    this.varNameSet = new Set();
    this.device = 'phone';//
    this.refresh = true;
    this.currQues = {quesId: ''};
    this.currPageIndex = 0;
    this.pageStack = [];//页面提交轨迹
    this.anId = undefined;//当前答案的主键
    this.version = 0 ;//版本号，时间来表示
    this.keep = false ;//断点续答
    this.quotaGroups = [];//配额
    this.endQues = {quesId: ''};
    this.startQues = {quesId: ''};
    this.cfg = {
      back: false,//是否支持回退操作，上一页、下一页按钮显示
      backBgColor: '',//上一页的按钮背景色
      backWidth: '',//上页的按钮宽度
      backTextAlign: 'right',//上页的位置
      backContent: '上一页',

      submitBgColor: '',
      submitWidth: '',
      submitContent: '提交',
      submitTextAlign: 'center',//提交按钮的位置

      surveyBackgroundColor: '#E4E7ED',
      quesBackgroundColor: '#FFFFFF',

      surveyPcBackgroundColor: '#E4E7ED',
      quesPcBackgroundColor: '#FFFFFF',

      surveyPhoneBackgroundColor: '#DADDE1',
      quesPhoneBackgroundColor: '#FFFFFF',
    }

    this.pages = [];
    this.tmpPages = [];//页面调整时的排序情况
  }

  defaultColor() {
    if (this.device == 'pc') {
      this.cfg.surveyPcBackgroundColor = '#E4E7ED';
      this.cfg.quesPcBackgroundColor = '#FFFFFF';
    } else {
      this.cfg.surveyPhoneBackgroundColor = '#DADDE1';
      this.cfg.quesPhoneBackgroundColor = '#FFFFFF';
    }
  }

  backBtDefault() {
    this.cfg.backBgColor = '';//上一页的按钮背景色
    this.cfg.backWidth = '';//上页的按钮宽度
    this.cfg.backTextAlign = 'right';//上页的位置
    this.cfg.backContent = '上一页';
  }

  submitBtDefault() {
    this.cfg.submitBgColor = '';
    this.cfg.submitWidth = '';
    this.cfg.submitContent = '提交';
    this.cfg.submitTextAlign = 'center';//提交按钮的位置
  }

  /**
   * 答题界面样式配置
   */
  anInit() {
    if (this.device == 'pc') {
      this.cfg.surveyBackgroundColor = this.cfg.surveyPcBackgroundColor;
      this.cfg.quesBackgroundColor = this.cfg.quesPcBackgroundColor;
    } else {
      this.cfg.surveyBackgroundColor = this.cfg.surveyPhoneBackgroundColor;
      this.cfg.quesBackgroundColor = this.cfg.quesPhoneBackgroundColor;
    }
  }

  calcQuesTypeSum(quesType) {
    let sum = 0;
    this.pages.forEach(page => {
      page.quess.forEach(ques => {
        if (ques.quesType == quesType) {
          sum++;
        }
      })
    })
    return sum;
  }

  changePage(pageIndex) {
    this.currPageIndex = pageIndex
    this.currQues = {quesId: ''}
  }

  getCurrPage() {
    return this.pages[this.currPageIndex]
  }

  /**
   * 当前问题之前的所有变量
   * @param quesId
   */
  getCurrQuesPreVarNames(quesId) {
    let preFlag = false;
    let varSet = new Set();
    this.pages.forEach(page => {
      page.quess.forEach(ques => {
        if (ques.quesId === quesId) {
          preFlag = true;//展示前面的问题，已答的题进行选择
        }
        if (!preFlag) {
          let currVarSet = ques.getVarNames()
          for (let varName of currVarSet) {
            varSet.add(varName)
          }
        }
      })
    })
    return varSet;
  }

  moveQues(pageIndex, quesIndex, flag = 'pre') {
    let ques = this.pages[pageIndex].quess[quesIndex]
    this.pages[pageIndex].quess.splice(quesIndex, 1);
    if (flag === 'pre') {
      if (quesIndex == 0) {//问题移到上页
        this.pages[pageIndex - 1].quess.push(ques);
        this.currPageIndex = pageIndex - 1
      } else {//本页上移
        this.pages[pageIndex].quess.splice(quesIndex - 1, 0, ques);
        this.currPageIndex = pageIndex
      }
    } else {
      let quesLen = this.pages[pageIndex].quess.length;
      if (quesIndex == quesLen) {//问题移到下页
        this.pages[pageIndex + 1].quess.splice(0, 0, ques);
        this.currPageIndex = pageIndex + 1
      } else {//本页下移
        this.pages[pageIndex].quess.splice(quesIndex + 1, 0, ques);
        this.currPageIndex = pageIndex
      }
    }
    this.currQues = ques
  }

  /**
   * 临时页面的移动排序
   * @param pageIndex
   * @param quesIndex
   * @param flag
   */
  moveTmpQues(pageIndex, quesIndex, flag = 'pre') {
    this.tmpPages = []
    for (let page of this.pages) {
      let tmpPage = new PageClass()
      tmpPage.pageId = page.pageId;
      this.tmpPages.push(tmpPage)
      for (let ques of page.quess) {
        tmpPage.quess.push(ques);
      }
    }
    let ques = this.tmpPages[pageIndex].quess[quesIndex]
    this.tmpPages[pageIndex].quess.splice(quesIndex, 1);
    if (flag === 'pre') {
      if (quesIndex == 0) {//问题移到上页
        this.tmpPages[pageIndex - 1].quess.push(ques);
      } else {//本页上移
        this.tmpPages[pageIndex].quess.splice(quesIndex - 1, 0, ques);
      }
    } else {
      let quesLen = this.tmpPages[pageIndex].quess.length;
      if (quesIndex == quesLen) {//问题移到下页
        this.tmpPages[pageIndex + 1].quess.splice(0, 0, ques);
      } else {//本页下移
        this.tmpPages[pageIndex].quess.splice(quesIndex + 1, 0, ques);
      }
    }
  }

  /**
   * 分页移动
   * @param pageIndex
   * @param flag
   */
  movePage(pageIndex, flag = 'pre') {
    let page = this.pages[pageIndex]
    if (flag === 'pre') {
      this.pages.splice(pageIndex, 1);
      this.pages.splice(pageIndex - 1, 0, page);
    } else {
      this.pages.splice(pageIndex, 1);
      this.pages.splice(pageIndex + 1, 0, page);
    }
  }

  /**
   * 临时页面的移动排序
   * @param pageIndex
   * @param flag
   */
  moveTmpPage(pageIndex, flag = 'pre') {
    this.tmpPages = []
    for (let page of this.pages) {
      this.tmpPages.push(page);
    }
    let page = this.tmpPages[pageIndex]
    if (flag === 'pre') {
      this.tmpPages.splice(pageIndex, 1);
      this.tmpPages.splice(pageIndex - 1, 0, page);
    } else {
      this.tmpPages.splice(pageIndex, 1);
      this.tmpPages.splice(pageIndex + 1, 0, page);
    }
  }


  deletePage(pageIndex) {
    if (this.pages.length == 1) {
      Message({
        message: '至少有一页！',
        type: 'warning'
      });
      return;
    }
    let currPage = this.getPageByIndex(pageIndex)
    let _params = {surveyId: this.surveyId, pageId: currPage.pageId}
    let _reqObj = {url: 'design/delPage', params: _params}
    return service.get(_reqObj, res => {
      this.pages.splice(pageIndex, 1);
      this.currPageIndex = 0
    })

    //if (this.currPageIndex === pageIndex) {
    //this.currPageIndex = 0
    //}
  }

  getPageByIndex(pageIndex) {
    for (let i = 0; i < this.pages.length; i++) {
      if (i == pageIndex) {
        return this.pages[i]
      }
    }
  }

  /**
   * 页面调整后的页面排序情况
   * @param flag
   * @returns {Array}
   */
  getChangePageBOs(flag) {
    let pageBOs = new Array();
    if (flag) {//从pages
      for (let page of this.pages) {
        let quesIds = new Array();
        pageBOs.push({pageId: page.pageId, quesIds: quesIds})
        for (let ques of page.quess) {
          quesIds.push(ques.quesId);
        }
      }
    } else {//从tmpPages
      for (let page of this.tmpPages) {
        let quesIds = new Array();
        pageBOs.push({pageId: page.pageId, quesIds: quesIds})
        for (let ques of page.quess) {
          quesIds.push(ques.quesId);
        }
      }
    }
    return pageBOs;
  }

  /**
   * 更新问卷的分页
   * @param survey
   * @returns {*}
   */
  updatePageBOs(pageBOs) {
    let _reqObj = {url: 'design/updatePageBOs?surveyId=' + this.surveyId, params: pageBOs}
    return service.post(_reqObj, res => {
      //console.log(JSON.stringify(res.data))
    })
  }

  updateCfg() {
    let _reqObj = {url: 'design/updateSurveyCfg?surveyId=' + this.surveyId, params: this.cfg}
    return service.post(_reqObj, res => {
      console.log(JSON.stringify(res.data))
    })
  }

  /**
   * 增加普通问题
   * @param survey
   * @param quesType
   * @param callJSON
   * @returns {*}
   */
  insertNormalQues(quesType, callJSON) {
    if (this.currPageIndex > 9999 || this.currPageIndex < 0) {
      this.currPageIndex = 0;
    }
    let currPage = this.getCurrPage()
    let currQues = QuesUtils.newQues(quesType)
    currQues.pageId = currPage.pageId
    currQues.surveyId = this.surveyId
    let _reqObj = {url: 'design/editQues', params: currQues}
    return service.post(_reqObj, res => {
      //console.log(JSON.stringify(res.data.res))
      currQues = QuesUtils.cloneQues(res.data.res)
      currPage.insertQues(currQues, this.currQues.quesId)
      callJSON.quesId = currQues.quesId
    })
  }
  copyQues(sourceQues, callJSON) {
    if (this.currPageIndex > 9999 || this.currPageIndex < 0) {
      this.currPageIndex = 0;
    }
    let currPage = this.getCurrPage()

    let _reqObj = {
      url: 'design/getCopyQues',
      params: {quesKey:sourceQues._id},
    }
    return SERVICE2(_reqObj).then(res => {
      let copyQues = QuesUtils.cloneQues(res.data.res)
      copyQues.pageId = currPage.pageId
      copyQues.surveyId = this.surveyId
      copyQues._id = undefined
      copyQues.quesId = undefined
      _reqObj = {url: 'design/editQues', params: copyQues}
      return service.post(_reqObj, res => {
        copyQues = QuesUtils.cloneQues(res.data.res)
        currPage.insertQues(copyQues, sourceQues.quesId)
        callJSON.quesId = copyQues.quesId
      })
    })
  }

  refQuesLib(quesKey, callJSON) {
    if (this.currPageIndex > 9999 || this.currPageIndex < 0) {
      this.currPageIndex = 0;
    }
    let currPage = this.getCurrPage()

    let _reqObj = {
      url: 'design/getQuesLib',
      params: {quesKey,ref:'Y'},
    }
    return SERVICE2(_reqObj).then(res => {
      let refQues = QuesUtils.cloneQues(res.data.res)
      refQues.pageId = currPage.pageId
      refQues.surveyId = this.surveyId
      refQues._id = undefined
      refQues.quesId = undefined
      _reqObj = {url: 'design/editQues', params: refQues}
      return service.post(_reqObj, res => {
        refQues = QuesUtils.cloneQues(res.data.res)
        currPage.insertQues(refQues, this.currQues.quesId)
        callJSON.quesId = refQues.quesId
      })
    })
  }

  scrollQues(VUE, quesId, flag = true) {
    VUE.$nextTick(
      () => {
        Fun.scrollQues(quesId, true)
      })
  }

  /**
   * 保存正在编辑的问题
   * @param survey
   * @returns {*}
   */
  saveQues() {
    if(this.lib=='Y'){
      let _reqObj = {url: 'design/editQuesLib', params: this.currQues}
      return service.post(_reqObj, res => {
        this.currQues = QuesUtils.cloneQues(res.data.res)
        Message({
          message: '保存成功！',
          type: 'success'
        });
      })
    }else {
      let currPage = this.getCurrPage()
      let _reqObj = {url: 'design/editQues', params: this.currQues}
      return service.post(_reqObj, res => {
        this.currQues = QuesUtils.cloneQues(res.data.res)
        currPage.updateQues(this.currQues)
        //this.state = 0;
        Message({
          message: '保存成功！',
          type: 'success'
        });
      })
    }
  }

  initVarNames() {
    this.varNameSet.clear();
      for (let page of this.pages) {
        for (let ques of page.quess) {
          let currVars = ques.getVarNames();
          for (let varName of currVars) {
            this.varNameSet.add(varName)
          }
        }
      }
  }
  /**
   * 提交时，跳转页判断
   * @returns {boolean}
   */
  jumpNextPage(tmpCurrPageIndex) {
    let currPage = this.pages[tmpCurrPageIndex]
    let pageIndex = null;//跳转哪页
    let endVal = null;//结束题选择哪个状态
    for (let ques of currPage.quess) {
      let visible = ques.getVisible(this.an, this.varNameSet);
      if (visible && ques.hasJumpEl()) {
        let elTree = new EL.ElTree()
        elTree.buildByFilter(ques.jumpEl)
        elTree.tree2Arr()
        let success = elTree.elLogicVal(this.an, this.varNameSet)
        if (success) {//条件满足，跳到指定页
          pageIndex = ques.jumpPage;
          if (pageIndex == 10000) {//如果跳到最后一题，
            endVal = ques.jumpEndVal;//结束题的状态
          }
          break;
        }
      }
    }
    if (!Fun.isNull(pageIndex)) {
      pageIndex = parseInt(pageIndex)
      if (pageIndex >= 10000) {
        if (!Fun.isNull(endVal)) {
          for (let row of this.endQues.rows) {
            if (row.val === endVal) {//跳转的状态必须在结束题有对应的状态
              this.an['end'] = endVal;
              break;
            }
          }
        }
        return 10000;
      }
      if (pageIndex < this.pages.length && pageIndex > 1) {//不能大于总页数
        if (pageIndex > tmpCurrPageIndex) {//大于上一页，页面调整时导致跳转错误
          return pageIndex
        }
      }
    }
    return null;
  }

  /**
   * 是否有问卷
   */
  hasSurvey() {
    //console.log(JSON.stringify(this.pages))
    if (Fun.isNull(this.pages)) {
      alert("还没有设计问卷");
      return false;
    }
    for (let page of this.pages) {
      if (Fun.isNull(page.quess)) {
        alert("部分页面没有增加题型");
        return false
      }
    }
    return true
  }

  /**
   * 当前页面使用的所有变量
   * @returns {Set}
   */
  getCurrPageVarNames() {
    let currPageVarNames = new Set();
    if (this.currPageIndex == 10000 || this.currPageIndex == -1) {
      if (this.currPageIndex == 10000) {
        currPageVarNames.add("end")
      }
    } else {
      for (let ques of this.pages[this.currPageIndex].quess) {
        let currVars = ques.getVarNames();
        for (let varName of currVars) {
          currPageVarNames.add(varName)
        }
      }
    }
    return currPageVarNames;
  }

  /**
   * 当前页面提交的答案
   * @returns {{}}
   */
  getCurrPageAn() {
    let currPageVarNames = this.getCurrPageVarNames();
    let currPageAn = {}
    for (let varName of currPageVarNames) {
      if (!Fun.isNull(this.an[varName])) {
        currPageAn[varName] = this.an[varName]
      }
    }
    if (!Fun.isNull(this.an['end'])) {//结束题答案
      currPageAn['end'] = this.an['end']
    }
    return currPageAn;
  }

  getRequestObj() {
    let wxSkip = false ;
    if(!Fun.isNull(this.wxSkip)){
      wxSkip = true;
    }
    let _params = {
      surveyId: this.surveyId,
      anId: this.anId,
      an: this.an,
      version:this.version,
      currAn: this.getCurrPageAn(),
      back: this.cfg.back,
      pageStack: this.pageStack,
      rtn:false,//
      wxSkip:wxSkip,
    }
    let _url = 'an/dati?surveyId='+this.surveyId

    let _reqObj = {url: _url, params: _params}
    return _reqObj;
  }

  submitEndPage() {
    this.setEndQuesAn();
    let _reqObj = this.getRequestObj();
    return service.post(_reqObj, res => {
      this.successHandlePageStack()
      //console.log(JSON.stringify(res.data))
      // console.log(JSON.stringify(res.data))
    })
  }

  /**
   * 1、手动点击提交,
   * 2、验证是否通过，
   * 3、收集答案，
   * 4、进入下一页
   */
  anValidSubmit() {
    let success = this.validAnswer();
    if (success) {
      let _reqObj = this.getRequestObj();
      let currNextPageIndex = this.currPageIndex;
      return service.post(_reqObj, res => {
        //console.log(JSON.stringify(res.data))
        this.anId = res.data.res.anId
        this.successHandlePageStack(_reqObj.params.currAn);//当前页的答案
        currNextPageIndex = this.loopNextPageIndex(currNextPageIndex)
        this.currPageIndex = currNextPageIndex;
      }).then(() => {
        this.handleStorageKeep(currNextPageIndex);//断点续答处理
        if (currNextPageIndex == 10000) {
          this.submitEndPage()
        }
      })
    }
  }

  getUnValidRequestObj(endVal) {
    let wxSkip = false ;
    if(!Fun.isNull(this.wxSkip)){
      wxSkip = true;
    }
    this.an['end'] = endVal
    let _currAn = this.getCurrPageAn()
    _currAn['end'] = endVal
    let _params = {
      surveyId: this.surveyId,
      anId: this.anId,
      an: this.an,
      version:this.version,
      currAn: _currAn,
      back: false,
      pageStack: this.pageStack,
      rtn:false,//
      wxSkip:wxSkip,
    }
    let _url = 'an/dati?surveyId='+this.surveyId
    let _reqObj = {url: _url, params: _params}
    return _reqObj;
  }
  /**
   * 答题时间过期，直接提交
   * @returns {*}
   */
  unValidSubmit() {
      let _reqObj = this.getUnValidRequestObj('Z');
      return service.post(_reqObj, res => {
        this.anId = res.data.res.anId
        Storage.removeStorage(this.surveyId);//清除缓层的答案
      })
  }

  getBackRequestObj() {
    let wxSkip = false ;
    let _params = {
      surveyId: this.surveyId,
      anId: this.anId,
      an: this.an,
      version:this.version,
      back: this.cfg.back,
      pageStack: this.pageStack,
      rtn:true,//返回上页
      wxSkip:wxSkip,
    }
    let _url = 'an/dati?surveyId='+this.surveyId

    let _reqObj = {url: _url, params: _params}
    return _reqObj;
  }

  /**
   * 返回上页
   * @returns {*|Promise.<TResult>}
   */
  anBackSubmit() {
    if(this.pageStack.length>0) {
      let prePageInfo = this.pageStack[this.pageStack.length - 1];//从页面堆栈弹出上页信息
      let currAn = prePageInfo.currAn;//上页提交的答案
      let _reqObj = this.getBackRequestObj();
      _reqObj.params.currAn = currAn
      return service.post(_reqObj, res => {
        this.anId = res.data.res.anId
        this.pageStack.pop();//删除最后一个
        //不删除上一页的答案
        //this.backDelAn(currAn);//删除样本里的当前页答案
        this.currPageIndex = prePageInfo.currPageIndex;//成功后，返回上一页
      }).then(() => {
        this.handleStorageKeep(this.currPageIndex);//断点续答处理
      })
    }
  }

  backDelAn(currAn){
    for(let key in currAn){
      delete this.an[key]
    }
  }

  /**
   * 答案提交成功后的堆栈页
   */
  successHandlePageStack(_currAn){
    this.pageStack.push({currPageIndex:this.currPageIndex,currAn:_currAn});//答题页面轨迹
  }


  /**
   * 处理断点续答存储功能
   */
  handleStorageKeep(currPageIndex){
    if(this.keep){//支持断点续答
      let surveyKey = this.surveyId

      if(currPageIndex>0&&currPageIndex<10000){//满足答题页范围
        let keepInfo = {
          version:this.version,
          hisPageIndex:currPageIndex,
          an: this.an,
          anId:this.anId,
          back: this.cfg.back,
          pageStack: this.pageStack,
        }
        Storage.setStorage(surveyKey, keepInfo)
      }else{
        Storage.removeStorage(surveyKey)
      }
    }
  }

  /**
   * 循环推断下一页，主要有的题型不显示和跳转页
   * @param currPageIndex
   * @returns {*}
   */
  loopNextPageIndex(currPageIndex) {
    if (currPageIndex >= 10000) {
      return 10000;
    }
    let showQuesCount = 0;
    let nextPageIndex = currPageIndex;
    while (showQuesCount == 0) {
      showQuesCount = 0;//所有问题都不显示，本页不显示
      nextPageIndex = this.getNextPageIndex(nextPageIndex);
      console.log("tmpCurrNextPageIndex=" + nextPageIndex)
      if (nextPageIndex >= 10000) {
        nextPageIndex = 10000;
        break;
      }
      let nextPage = this.pages[nextPageIndex];
      let visible = true
      for (let ques of nextPage.quess) {
        if (ques.hasVisibleEl()) {
          visible = ques.getVisible(this.an, this.varNameSet)
        }
        if (visible) {
          showQuesCount++;
        }
      }
    }
    return nextPageIndex;
  }

  /**
   * 给结束题，设置默认值
   */
  setEndQuesAn() {
    let endAn = this.an['end'];
    if (Fun.isNull(endAn)) {
      this.an['end'] = 'A';
      for (let row of this.endQues.rows) {
        if (row.dft) {
          this.an['end'] = row.val;
          break;
        }
      }
    }
  }

  /**
   * 提交后的下一页索引
   * @returns {boolean}
   */
  getNextPageIndex(tmpCurrPageIndex) {
    let nextPageIndex = this.jumpNextPage(tmpCurrPageIndex);
    if (Fun.isNull(nextPageIndex)) {
      nextPageIndex = tmpCurrPageIndex + 1;
      if (nextPageIndex > this.pages.length - 1) {
        nextPageIndex = 10000; //结束题
      }
    }
    return nextPageIndex;
  }


  /**
   * 当前页提交前进行答案的验证
   * @returns {boolean}
   */
  validAnswer() {
    let success = true;
    let tipErr = false ;
    for (let ques of  this.pages[this.currPageIndex].quess) {
      let visible = ques.getVisible(this.an, this.varNameSet)
      if (visible) {
        let valid = ques.anValid(this.an)
        if (!valid) {//只要有没有验证通过的
          success = valid;
          if(!tipErr) {//回滚到错误题位置
            let anchor = document.querySelector("#an" + ques.quesId);
            if (anchor != null) {
              document.body.scrollTop = anchor.offsetTop;
              document.documentElement.scrollTop = anchor.offsetTop;
            }
            tipErr = true
          }
          //break;
        }
      }
    }
    return success;
  }

  /**
   * 插入分页
   * @param page
   * @param pageIndex
   */
  insertPage(page, pageIndex = -1) {
    if (pageIndex === -1) {
      this.pages.push(page)
    } else {
      this.pages.splice(pageIndex + 1, 0, page);
    }
    this.currPageIndex = this.pages.length - 1;
  }

  addPage() {
    let _reqObj = {url: 'design/insertPage', params: {surveyId: this.surveyId}}
    return service.get(_reqObj, res => {
      let pageId = res.data.res.pageId;
      let page = new PageClass()
      page.pageId = pageId;
      this.insertPage(page)
    })
  }

  /**
   * 编辑时从数据库去问卷数据
   */
  getEditSurveyStructure(flag) {
    let _reqObj = {
      url: 'design/getEditSurveyStructure',//从表里取数
      params: {surveyId: this.surveyId}
    }
    return SERVICE2(_reqObj).then(res => {
      this.initSurveyStructure(res.data.res,flag);
    })
  }

  /**
   * 初始化问卷结构
   */
  initSurveyStructure(res,flag){
    let dbSurvey = res.survey;
    let dbQuess = res.quess;//问卷的所有问题
    // if(!design){
    //   this.version = res.data.res.version ;//问卷版本号
    // }
    for (let k of Object.keys(dbSurvey)) {//问卷基本属性更新
      this[k] = dbSurvey[k];//使用数据的问卷属性，进行更新
    }
    if(flag!=undefined&&flag){//一页显示所有问题
      let pageOne = new PageClass()
      pageOne.pageId = 'pageId0001';
      this.insertPage(pageOne)
      for (let pageBO of this.pageBOs) {//每个页面
        this.initPageQuess(pageOne, pageBO.quesIds, dbQuess);
      }
    }else {
      for (let pageBO of this.pageBOs) {//每个页面
        let pageOne = new PageClass()
        pageOne.pageId = pageBO.pageId;
        this.insertPage(pageOne)
        this.initPageQuess(pageOne, pageBO.quesIds, dbQuess);
      }
    }
    this.initEndQues(dbQuess);
    this.initStartQues(dbQuess);
  }

  initPageQuess(page, quesIds, quess) {
    for (let quesId of quesIds) {
      for (let ques of quess) {
        if (quesId == ques.quesId) {
          let quesClass = QuesUtils.cloneQues(ques, false);
          page.quess.push(quesClass)
          break;
        }
      }
    }
  }

  initStartQues(quess) {
    for (let ques of quess) {
      if (ques.quesType == 'Start') {
        let quesClass = QuesUtils.cloneQues(ques, false);
        this.startQues = quesClass;
        if (this.startQues.use) {
          this.currPageIndex = -1
        } else {
          this.currPageIndex = 0
        }
        return;
      }
    }
    this.currPageIndex = 0
  }

  initEndQues(quess) {
    for (let ques of quess) {
      if (ques.quesType == 'End') {
        let quesClass = QuesUtils.cloneQues(ques, false);
        this.endQues = quesClass;
        break;
      }
    }
  }
}

export default SurveyClass





