import service from "../config/service.js";
import {MessageBox} from "element-ui";
/**
 * 分页
 */
class PageClass {
  constructor() {
    this.pageId = '';
    this.collapse = false;
    this.quess = [];
  }

  delQues(surveyId, quesIndex) {
    MessageBox.confirm('确定删除此问题！', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(() => {
      let currQues = this.getQuesByIndex(quesIndex)
      let _params = {surveyId: surveyId, pageId: this.pageId, quesId: currQues.quesId}
      let _reqObj = {url: 'design/delQues', params: _params}
      return service.get(_reqObj, res => {
        this.quess.splice(quesIndex, 1);
      })
    });
  }

  getQuesByIndex(quesIndex) {
    for (let i = 0; i < this.quess.length; i++) {
      if (i == quesIndex) {
        return this.quess[i]
      }
    }
  }

  // getQues(quesId){
  //   for (let ques of this.quess) {
  //     if (ques.quesId === quesId) {
  //       return ques
  //     }
  //   }
  // }
  updateQues(ques) {
    let index = -1
    for (let i = 0; i < this.quess.length; i++) {
      if (this.quess[i].quesId === ques.quesId) {
        index = i;
        break;
      }
    }
    if (index != -1) {
      this.quess.splice(index, 1, ques)
    }
  }

  insertQues(ques, quesId = '') {
    let index = -1
    for (let i = 0; i < this.quess.length; i++) {
      if (this.quess[i].quesId === quesId) {
        index = i;
        break;
      }
    }
    if (index === -1) {
      this.quess.push(ques)
    } else {
      this.quess.splice(index + 1, 0, ques);
    }
  }
}

export default PageClass





