<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        {{horChange}}
        <table :style="{width:ques.width}">
          <template v-for="(rowArr,rowIndex) in tableArr">
            <tr>
              <td v-for="(item,index) in rowArr">
                <el-card :body-style="{ padding:'3px',textAlign:'center'}">
                  <label>
                    <div style="text-align: center">
                        <img :class="ques.imgShape" :src="item.imgUrl"
                             :style="{width:ques.imgWidth,height:ques.imgHeight}">
                      </div>
                    <div style="padding:6px 0">
                      <div>
                        <el-radio v-model="survey.an[ques.varName]" :label="item.val">
                          &nbsp;
                        </el-radio>
                        <div style="display: inline-block" v-html="item.htmlLabel"></div>
                      </div>
                    </div>
                  </label>
                </el-card>

              </td>
            </tr>
          </template>
        </table>
        <div style="height:3px"></div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'PicRadioView',
    components: {
      ViewQuesT,
    },
    props: ['survey', 'ques'],
    data () {
      return {
        colSize: 1,//每行几列
        tableArr: new Array(),
      }
    },
    created(){
    },
    mounted(){

    },
    methods: {
      changeLayout(){
        this.tableArr = new Array()
        this.colSize = Number.parseInt(this.ques.horCol);//每行几列

        this.ques.rows.forEach((item, index) => {
          let rowIndex = Math.floor(index / this.colSize);//向下取整
          let rowArr = null
          if (this.tableArr.length > rowIndex) {
            rowArr = this.tableArr[rowIndex];
          } else {
            rowArr = new Array()
            this.tableArr.push(rowArr)
          }
          rowArr.push(item)
        });
      }
    },
    computed: {
      horChange: function (newVal, oldVal) {
        this.changeLayout();
      }
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  table td {
    padding: 5px 3px;
  }

  .voteMask {
    /**display: none;*/
    position: absolute;
    color: #fff;
    line-height: 100%;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    font-weight: normal;
    left: 25%;
    top: 25%;
    width: 50%;
    height: 50%;
    background: #323131;
    opacity: 0.8;
    z-index: 100;
  }
</style>



