<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        {{indexsC}}
        <table   :style="{width:ques.width}">
          <template v-for="(itemRow,indexRow) in ques.rows">
            <tr>
              <td colspan="3">
                <div v-html="itemRow.htmlGroup"></div>
              </td>
            </tr>
            <tr>
              <td>
                <div style="min-width:45px" v-html="itemRow.htmlLabel"></div>
              </td>
              <template v-for="(itemCol,indexCol) in ques.cols">
                <td style="text-align:center;">
                  <i @click="clickRate(itemRow,itemCol,indexRow,indexCol)" @mouseenter="enterRate(indexRow,indexCol)"
                     @mouseleave="leaveRate(indexRow,indexCol)" class='iconfont'
                     style="padding-right:5px;" :title="itemCol.htmlLabel"
                     :style="rateStyle(indexRow,indexCol)" v-html="ques.icon">
                  </i>
                </td>
              </template>
            </tr>
          </template>
        </table>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'IconMatrixRateView',
    components: {
      ViewQuesT,
    },
    props: ['survey', 'ques'],
    data () {
      return {
        indexs:[]
      }
    },
    mounted(){
    },
    methods: {
      leaveRate(indexRow,indexCol){
        this.indexs[indexRow].currIndex = this.indexs[indexRow].oldCurrIndex
      },
      enterRate(indexRow,indexCol){
        this.indexs[indexRow].oldCurrIndex = this.indexs[indexRow].currIndex
        this.indexs[indexRow].currIndex = indexCol
      },
      rateStyle(indexRow,indexCol){

        let color = this.ques.unActiveColor;
        let iconSize = this.ques.iconSize + 'px';
        if (indexCol <= this.indexs[indexRow].currIndex) {
          color = this.ques.activeColor;
        }
        if (indexCol == this.indexs[indexRow].currIndex) {
          iconSize = (this.ques.iconSize) + 'px';
        }
        let style = 'color:' + color + ';fontSize:' + iconSize;
        return style;

      },
      clickRate(itemRow,itemCol,indexRow,indexCol){
        this.indexs[indexRow].currIndex = indexCol;
        this.indexs[indexRow].oldCurrIndex = indexCol;
        this.survey.an[itemRow.varName] = itemCol.val
      },
    },
    computed: {
      indexsC(){//选项变动时，重新加载索引
        this.indexs.splice(0,this.indexs.length)
        for (let item of this.ques.rows) {
          this.indexs.push({currIndex:-1,oldCurrIndex:-1})
          this.$set(this.survey.an, item.varName, undefined);
        }
      }
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  table td {
    padding: 0px 5px 3px 5px;
  }
</style>



