<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        {{horChange}}
        <table :style="{width:ques.width}">
          <template v-for="(rowArr,rowIndex) in tableArr">
            <tr>
              <td v-for="(item,index) in rowArr">
                <el-card :body-style="{ padding:'3px',textAlign:'center'}">
                  <label>
                    <div style="text-align: center">
                      <img :class="ques.imgShape" :src="item.imgUrl"
                           :style="{width:ques.imgWidth,height:ques.imgHeight}">
                    </div>
                    <div style="padding:6px 0">
                      <div>
                        <el-checkbox v-if="show" @change="optionChange(item)" :true-label="1"
                                     v-model="survey.an[item.varName]">&nbsp;
                        </el-checkbox>
                        <div style="display: inline-block" v-html="item.htmlLabel"></div>
                      </div>
                    </div>
                  </label>
                </el-card>
              </td>
            </tr>
          </template>
        </table>
        <div style="height:3px"></div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'PicCheckboxView',
    components: {
      ViewQuesT
    },
    props: ['survey', 'ques'],
    data () {
      return {
        colSize: 1,//每行几列
        tableArr: new Array(),
        show: true,
        hisMap: new Map()
      }
    },
    mounted(){

    },
    methods: {
      optionChange(item){
        this.optionMutex(item)
      },

      /**
       *互斥控制
       * @param item
       */
      optionMutex(item){
        if (item.mutex) {//选中互斥选项
          for (let _item of this.ques.rows) {
            if (_item !== item) {//所有其他选项置空
              if (!this.Fun.isNull(this.survey.an[_item.varName])) {
                this.survey.an[_item.varName] = undefined;
                this.$set(this.survey.an, _item.varName, undefined);
                this.show = false;
              }
            }
          }
        } else {//选中非互斥选项
          for (let _item of this.ques.rows) {
            if (_item.mutex) {//所有互斥选项置空
              if (!this.Fun.isNull(this.survey.an[_item.varName])) {
                this.survey.an[_item.varName] = undefined;//
                this.$set(this.survey.an, _item.varName, undefined);
                this.show = false;
              }
            }
          }
        }
        if (!this.show) {
          this.$nextTick(
            () => {
              this.show = true;
            }
          )
        }
      },
      changeLayout(){
        this.tableArr = new Array()
        this.colSize = Number.parseInt(this.ques.horCol);//每行几列

        this.ques.rows.forEach((item, index) => {
          let rowIndex = Math.floor(index / this.colSize);//向下取整
          let rowArr = null
          if (this.tableArr.length > rowIndex) {
            rowArr = this.tableArr[rowIndex];
          } else {
            rowArr = new Array()
            this.tableArr.push(rowArr)
          }
          rowArr.push(item)
        });
      },
    },
    computed: {
      horChange: function (newVal, oldVal) {
        this.changeLayout();
      }
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>

  table td {
    padding: 5px 3px;
  }
</style>



