<template>
  <div>
    <default-radio-rate-view v-if="ques.style==''"
        :survey="survey" :ques="ques"></default-radio-rate-view>
    <icon-radio-rate-view v-else-if="ques.style=='icon'"
                               :survey="survey" :ques="ques">
    </icon-radio-rate-view>
  </div>
</template>

<script>
  import DefaultRadioRateView from './view/DefaultRadioRateView'
  import IconRadioRateView from './view/IconRadioRateView'
  export default {
    name: 'IndexRadioRateView',
    components: {
      DefaultRadioRateView,IconRadioRateView
    },
    props: ['survey','ques'],
    data () {
      this.$set(this.survey.an, this.ques.varName, undefined);
      return {}
    },
    mounted(){

    },
    methods: {

    },
    computed: {},
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>

</style>



