<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        {{horChange}}
        <table v-if="show" :style="{width:ques.width}">
          <template v-for="(rowArr,rowIndex) in tableArr">
            <tr>
              <td v-for="(item,index) in rowArr">
                <label class="activerow" style="display:flex;align-items:center;">
                  <div :style="{width:ques.width1}"
                       style="display:flex;align-items:center;">
                    <el-checkbox @change="optionChange(item)" :true-label="1"
                                 v-model="survey.an[item.varName]">&nbsp;
                    </el-checkbox>
                    <div v-html="item.htmlLabel"></div>
                  </div>
                  <div v-if="item.fillFlag"
                       style="width:100%;display: flex;">
                    <input v-model="survey.an[item.varNameT]" @click="survey.an[item.varName]=item.val"
                           style="width:100%" class="underline" type="text">
                    <span v-if="item.fillRequired" class="required"></span>
                  </div>
                </label>
              </td>
            </tr>
          </template>
        </table>
        <div style="height:3px"></div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultCheckboxView',
    components: {
      ViewQuesT
    },
    props: ['survey', 'ques'],
    data () {
      return {
        colSize:1,//每行几列
        tableArr:new Array(),
        show: true,
        hisMap: new Map()
      }
    },

    mounted(){
    },
    methods: {
      optionChange(item){
        this.optionTHis(item)
        this.optionMutex(item)
      },
      /**
       * 控制文本输入框在选中或不选中下，内容是否回显
       */
      optionTHis(item){
        if (this.Fun.isNull(this.survey.an[item.varName])) {
          if (!this.Fun.isNull(this.survey.an[item.varNameT])) {
            this.hisMap.set(item.varNameT, this.survey.an[item.varNameT])
            this.survey.an[item.varNameT] = undefined
          }
        } else {
          let valT = this.hisMap.get(item.varNameT)
          if (valT != null) {
            this.survey.an[item.varNameT] = valT
          }
        }
      },
      /**
       *互斥控制
       * @param item
       */
      optionMutex(item){
        if (item.mutex) {//选中互斥选项
          for (let _item of this.ques.rows) {
            if (_item !== item) {//所有其他选项置空
              if (!this.Fun.isNull(this.survey.an[_item.varName])) {
                this.survey.an[_item.varName] = undefined;
                this.$set(this.survey.an, _item.varName, undefined);
                this.show = false;
              }
            }
          }
        } else {//选中非互斥选项
          for (let _item of this.ques.rows) {
            if (_item.mutex) {//所有互斥选项置空
              if (!this.Fun.isNull(this.survey.an[_item.varName])) {
                this.survey.an[_item.varName] = undefined;//
                this.$set(this.survey.an, _item.varName, undefined);
                this.show = false;
              }
            }
          }
        }
        if (!this.show) {
          this.$nextTick(
            () => {
              this.show = true;
            }
          )
        }
      },
      changeLayout(){
        this.tableArr = new Array()
        this.colSize = Number.parseInt(this.ques.horCol);//每行几列
        this.ques.rows.forEach((item,index) => {
          let rowIndex = Math.floor(index/this.colSize);//向下取整
          let rowArr = null
          if(this.tableArr.length>rowIndex){
            rowArr = this.tableArr[rowIndex];
          }else{
            rowArr = new Array()
            this.tableArr.push(rowArr)
          }
          rowArr.push(item)
        });
      }
    },
    computed: {
      horChange: function (newVal, oldVal) {
        this.changeLayout();
      }
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>

  table td {
    padding: 5px 0;
  }
</style>



