<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        <div style="height:3px;"></div>
        <div style="display:flex;padding-left:2px">
          <div v-html="ques.leftTitle"></div>
          <input v-if="ques.row==1" v-model="survey.an[ques.varName]"
                 :style="{width:ques.width}" :type="Fun.inputType(ques.rule)" class="underline">
          <textarea v-if="ques.row>1" v-model="survey.an[ques.varName]"
                    :style="{width:ques.width}" class="form-control input-sm"
                    :rows="ques.row"></textarea>
          <div v-html="ques.rightTitle"></div>
        </div>
        <div style="height:5px;"></div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultInputView',
    components: {
      ViewQuesT,
    },
    props: ['survey', 'ques'],
    data () {
      return {
      }
    },

    mounted(){
    },
    methods: {

    },
    computed: {},
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
