<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        {{horChange}}
        <table :style="{width:ques.width}">
          <template v-for="(rowArr,rowIndex) in tableArr">
            <tr>
              <td v-for="(item,index) in rowArr">
                <el-card :body-style="{ padding:'3px',textAlign:'center'}">
                  <label>
                    <div style="text-align: center">
                      <div style="position:relative;margin:auto" :style="{width:ques.imgWidth,height:ques.imgHeight}">
                        <div v-if="show" class="voteMask">
                          <div>
                            <div>
                              <span style="color: white">{{voteRes[item.fieldName].rate}}%</span>
                            </div>
                            <div style="margin-top:5px">
                              <span
                                style="color: white">{{voteRes[item.fieldName].count}}票</span>
                            </div>
                          </div>
                        </div>
                        <img :class="ques.imgShape" :src="item.imgUrl"
                             :style="{width:ques.imgWidth,height:ques.imgHeight}">
                      </div>
                    </div>
                    <div style="padding:6px 0">
                      <div>
                        <el-checkbox @change="calcRate(item.fieldName,survey.an[item.varName])" :true-label="1"
                                     v-model="survey.an[item.varName]">&nbsp;
                        </el-checkbox>
                        <div style="display: inline-block" v-html="item.htmlLabel"></div>
                      </div>
                    </div>
                  </label>
                </el-card>

              </td>
            </tr>
          </template>
        </table>
        <div style="height:3px"></div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultCheckboxVoteView',
    components: {
      ViewQuesT
    },
    props: ['survey', 'ques'],
    data () {
      return {
        colSize: 1,//每行几列
        tableArr: new Array(),
        pager: {
          currPage: 0,
          total: 0,
          logic: true,
          pageSize: 10,
          sorts: [{col: 'created', op: 'desc'}],
          filters: [],
        },
        hisMap: new Map(),
        show: false,
        voteRes: {},
        total: 1,
      }
    },

    mounted(){
      if (this.survey.model == 1) {//答题测试模式
        this.fetchData();
      }
    },
    methods: {
      calcRate(fieldName,checkVal){
        if (this.survey.model == 1&&this.ques.voteShow) {
          this.show = true
          for (let key in this.voteRes) {
            if (fieldName == key) {
              if(checkVal==undefined){
                this.voteRes[key].count = this.voteRes[key].count - 1
              }else{
                this.voteRes[key].count = this.voteRes[key].count + 1
              }
              break;
            }
          }
          for (let key in this.voteRes) {
            this.voteRes[key].rate = ((this.voteRes[key].count / (this.total+1)) * 100).toFixed(2)
          }
        }
      },
      fetchData(){
        let _reqObj = {
          url: 'pub/checkboxRes',
          data: {pager: this.pager, quesKey: this.ques._id, surveyId: this.survey.surveyId},
          method: 'post',
        }
        return this.SERVICE2(_reqObj).then(res => {
          this.total = res.data.res.total
          for (let row of this.ques.rows) {//初始化每个选项的投票值
            let vote = {rate: 0, count: 0}
            this.voteRes[row.fieldName] = vote
            for (let key in res.data.res.voteRes) {
              if (row.fieldName == key) {
                vote.count = res.data.res.voteRes[key]
              }
            }
          }
        })
      },
      changeLayout(){
        this.tableArr = new Array()
        this.colSize = Number.parseInt(this.ques.horCol);//每行几列

        this.ques.rows.forEach((item, index) => {
          let rowIndex = Math.floor(index / this.colSize);//向下取整
          let rowArr = null
          if (this.tableArr.length > rowIndex) {
            rowArr = this.tableArr[rowIndex];
          } else {
            rowArr = new Array()
            this.tableArr.push(rowArr)
          }
          rowArr.push(item)
        });
      },
    },
    computed: {
      horChange: function (newVal, oldVal) {
        this.changeLayout();
      }
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>

  table td {
    padding: 5px 3px;
  }
  .voteMask {
    /**display: none;*/
    position: absolute;
    color: #fff;
    line-height: 100%;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    font-weight: normal;
    left: 25%;
    top: 25%;
    width: 50%;
    height: 50%;
    background: #323131;
    opacity: 0.8;
    z-index: 100;
  }
</style>



