import {BaseOptionClass, BaseQuesClass} from "./BaseClass";
import Fun from "../Fun";

/**
 * 单选题选项
 */
class MulGoodsOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.val = Fun.getValInc();
    this.varName = 'L_'+Fun.getInc();
    this.fieldName = this.varName;
  }
}
/**
 *
 */
class MulGoodsClass extends BaseQuesClass {
  constructor(design = true) {
    super();
    this.quesType = 'MulGoods';
    this.htmlTitle = '<p><strong>商品题</strong></p>';
    this.min = 0;//最少个数
    this.max = 0;
    this.colspan = 1;//每行的列数
    this.pcColspan = 3;
    this.phoneColspan = 1;
    this.rows = [];
    if (design) {//设计问题
      this.init();
    }
  }

  /**
   * 验证答题
   * @param an
   * @returns {boolean}
   */
  anValid(an) {
    super.anValid(an);
    if (!this.required) {
      return true
    }
    let sum = 0;
    let count = 0;
    for (let item of this.rows) {
      sum += an[item.varName]
      if (an[item.varName] > 0) {
        count++;
      }
    }
    if (sum == 0) {
      this.errInfo = "这道题必须选择商品哦！"
      return false
    }
    if (this.anHasOptionSum()) {//选项数量验证
      if (this.min > count || this.max < count) {
        this.errInfo = "填写个数不满足要求！"
        return false;
      }
    }
    this.errInfo = ""
    return true;
  }

  getVarNames() {
    let varSet = new Set();
    for (let item of this.rows) {
      varSet.add(item.varName)
    }
    return varSet
  }
  /**
   * 答题界面样式等初始配置
   */
  anInit() {
    super.anInit();
    if (this.device == 'pc') {
      this.colspan = this.pcColspan;
    } else {
      this.colspan = this.phoneColspan;
    }
  }

  /**
   * 是否配置选项数量控制
   * @returns {boolean}
   */
  anHasOptionSum() {
    if (this.max != 0 && (this.min <= this.max)) {
      if (this.max <= this.rows.length) {
        return true;
      }
    }
    return false;
  }

  anOptionSumInfo() {
    let start = this.min > 0 ? '请填写' + this.min + '-' : '最多填写'
    return '[' + start + this.max + '项]'
  }


  init() {
    let labels = ['<p>商品1</p>', '<p>商品2</p>']
    let rate = null
    for (let i = 0; i < labels.length; i++) {
      rate = new MulGoodsOptionClass();
      rate.val = "" + (i + 1)
      rate.htmlLabel = labels[i]
      this.rows.push(rate)
    }
  }

  addOption(index) {
    let option = new MulGoodsOptionClass();
    option.htmlLabel = "<p>商品" + (this.rows.length + 1)+"</p>";
    option.val = "" + (this.rows.length + 1)
    if (index == -1) {//尾部插入
      this.rows.push(option)
    } else {//之后插入
      this.rows.splice(index + 1, 0, option);
    }
  }

  delOption(index) {
    if (this.rows.length > 1) {
      this.rows.splice(index, 1)
    }
  }

  batchAddOptions(batchLabels) {
    batchLabels.forEach(label => {
      let option = new MulGoodsOptionClass();
      option.htmlLabel = label;
      this.rows.push(option)
    })
  }

  /**
   * 获取批量显示的选项标签,用于打开批量增加选项对话框的默认值
   */
  getBatchLabels() {
    let htmlLabels = []
    this.rows.forEach((option) => {
      let htmlLabel = option.htmlLabel;
      if (!Fun.isNull(htmlLabel)) {
        htmlLabels.push(htmlLabel)
      }
    })
    return htmlLabels;
  }

  /**
   * 上下移动选项
   * @param index
   * @param flag
   */
  moveOption(index, flag = 'pre') {
    let currOption = this.rows[index];
    if (flag === 'pre') {//上移
      this.rows.splice(index, 1);//删除
      this.rows.splice(index - 1, 0, currOption);//再插入
    } else {
      this.rows.splice(index, 1);//删除
      this.rows.splice(index + 1, 0, currOption);//再插入
    }
  }
}
export default MulGoodsClass




