<template>
  <div class="ques">
    <index-radio-view v-if="ques.quesType==='Radio'"
                      :survey="survey" :ques="ques"></index-radio-view>
    <index-checkbox-view v-else-if="ques.quesType==='Checkbox'"
                         :survey="survey" :ques="ques"></index-checkbox-view>
    <index-input-view v-else-if="ques.quesType==='Input'"
                      :survey="survey" :ques="ques"></index-input-view>
    <index-mul-input-view v-else-if="ques.quesType==='MulInput'"
                          :survey="survey" :ques="ques"></index-mul-input-view>
    <index-matrix-radio-view v-else-if="ques.quesType==='MatrixRadio'"
                             :survey="survey" :ques="ques"></index-matrix-radio-view>
    <index-matrix-checkbox-view v-else-if="ques.quesType==='MatrixCheckbox'"
                                :survey="survey" :ques="ques"></index-matrix-checkbox-view>
    <index-matrix-input-view v-else-if="ques.quesType==='MatrixInput'"
                             :survey="survey" :ques="ques"></index-matrix-input-view>
    <index-radio-rate-view v-if="ques.quesType==='RadioRate'"
                           :survey="survey" :ques="ques"></index-radio-rate-view>
    <index-matrix-rate-view v-else-if="ques.quesType==='MatrixRate'"
                            :survey="survey" :ques="ques"></index-matrix-rate-view>
    <index-desc-view v-else-if="ques.quesType==='Desc'"
                     :survey="survey" :ques="ques"></index-desc-view>
    <index-file-view v-else-if="ques.quesType==='File'"
                     :survey="survey" :ques="ques"></index-file-view>
    <index-matrix-scroll-view v-else-if="ques.quesType==='MatrixScroll'"
                              :survey="survey" :ques="ques"></index-matrix-scroll-view>
    <index-input-scroll-view v-else-if="ques.quesType==='InputScroll'"
                             :survey="survey" :ques="ques"></index-input-scroll-view>
    <index-mul-sort-view v-if="ques.quesType==='MulSort'"
                         :survey="survey" :ques="ques"></index-mul-sort-view>
    <index-matrix-select-view v-else-if="ques.quesType==='MatrixSelect'"
                              :survey="survey" :ques="ques"></index-matrix-select-view>
    <index-matrix-scale-view v-else-if="ques.quesType==='MatrixScale'"
                             :survey="survey" :ques="ques"></index-matrix-scale-view>
    <index-radio-select-view v-else-if="ques.quesType==='RadioSelect'"
                             :survey="survey" :ques="ques"></index-radio-select-view>
    <index-radio-cascader-view v-else-if="ques.quesType==='RadioCascader'"
                               :survey="survey" :ques="ques"></index-radio-cascader-view>
    <index-mul-goods-view v-else-if="ques.quesType==='MulGoods'"
                          :survey="survey" :ques="ques"></index-mul-goods-view>
    <index-radio-vote-view v-else-if="ques.quesType==='RadioVote'"
                          :survey="survey" :ques="ques"></index-radio-vote-view>
    <index-checkbox-vote-view v-else-if="ques.quesType==='CheckboxVote'"
                           :survey="survey" :ques="ques"></index-checkbox-vote-view>
    <index-start-view v-else-if="ques.quesType==='Start'"
                      :survey="survey" :ques="ques"></index-start-view>
    <index-end-view v-else-if="ques.quesType==='End'"
                    :survey="survey" :ques="ques"></index-end-view>
    {{changeDevice}}
    {{clearErr}}
    <div v-if="ques.quesType!='Start'&&ques.quesType!='End'">
      <div v-show="survey.state == 1" style="height:15px"></div>
      <div v-show="survey.state == 1" :style="{width:device=='pc'?'345px':'100%'}" style="text-align: center">
        <el-button type="primary" style="width:98%;font-size:16px" @click="ques.anValid(survey.an)">
          初测答题填写是否有错！<i class="el-icon-view el-icon--right"></i>
        </el-button>
      </div>
    </div>
  </div>

</template>

<script>
  import IndexRadioView from './radio/IndexRadioView'
  import IndexCheckboxView from './checkbox/IndexCheckboxView'
  import IndexInputView from './input/IndexInputView'
  import IndexMulInputView from './mulinput/IndexMulInputView'
  import IndexMatrixRadioView from './matrixradio/IndexMatrixRadioView'
  import IndexMatrixCheckboxView from './matrixcheckbox/IndexMatrixCheckboxView'
  import IndexMatrixInputView from './matrixinput/IndexMatrixInputView'
  import IndexRadioRateView from './radiorate/IndexRadioRateView'
  import IndexMatrixRateView from './matrixrate/IndexMatrixRateView'
  import IndexDescView from './desc/IndexDescView'
  import IndexFileView from './file/IndexFileView'
  import IndexMatrixScrollView from './matrixscroll/IndexMatrixScrollView'
  import IndexInputScrollView from './inputscroll/IndexInputScrollView'
  import IndexMulSortView from './mulsort/IndexMulSortView'
  import IndexMatrixSelectView from './matrixselect/IndexMatrixSelectView'
  import IndexMatrixScaleView from './matrixscale/IndexMatrixScaleView'
  import IndexRadioSelectView from './radioselect/IndexRadioSelectView'
  import IndexRadioCascaderView from './radiocascader/IndexRadioCascaderView'
  import IndexMulGoodsView from './mulgoods/IndexMulGoodsView'
  import IndexRadioVoteView from './radiovote/IndexRadioVoteView'
  import IndexCheckboxVoteView from './checkboxvote/IndexCheckboxVoteView'
  import IndexStartView from './start/IndexStartView'
  import IndexEndView from './end/IndexEndView'

  export default {
    name: 'ViewQues',
    components: {
      IndexRadioView, IndexCheckboxView, IndexInputView,
      IndexMulInputView, IndexMatrixRadioView, IndexMatrixCheckboxView,
      IndexMatrixInputView, IndexRadioRateView, IndexMatrixRateView,
      IndexDescView, IndexFileView, IndexMatrixScrollView, IndexInputScrollView,
      IndexMulSortView, IndexMatrixSelectView, IndexMatrixScaleView,
      IndexRadioSelectView, IndexRadioCascaderView, IndexMulGoodsView,
      IndexRadioVoteView,IndexCheckboxVoteView,IndexStartView, IndexEndView
    },
    props: ['survey', 'ques', 'device'],
    data () {
      return {}
    },
    created(){
      this.ques.anDefaultVal(this, this.survey.an)
    },
    mounted(){
      this.ques.device = this.device
      this.ques.anInit()
      if (this.survey.model == 1) {//答题测试模式
        this.ques.anRandom()
      }
    },
    beforeDestroy(){

    },
    methods: {},
    computed: {
      clearErr: function () {
        this.ques.errInfo = ""
        this.survey.an;//监听答案变化
      },
      changeDevice(){
        this.ques.device = this.device
        this.ques.anInit()
        //return this.device
      }
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>

</style>



