import {BaseQuesClass,BaseOptionClass} from './BaseClass'
import Fun from '../Fun'

  /**
   * 单选题选项
   */
  class RadioSelectOptionClass extends BaseOptionClass {
    constructor() {
      super();
      this.val = Fun.getValInc();
      this.htmlGroup = '';
      this.default = false;//默认选中
      this.noRandom = false;//选项固定，在使用随机显示选项才起作用
    }
  }
  /**
   * 单选题
   */
  class RadioSelectClass extends BaseQuesClass {
    constructor(design=true) {
      super();
      this.quesType = 'RadioSelect';
      this.htmlTitle = '<p><strong>下拉题</strong></p>';
      this.varName = 'R_'+Fun.getInc();
      this.fieldName = this.varName;
      this.random = false;//选项随机
      this.width = '' ;
      this.pcWidth = '' ;
      this.phoneWidth = '100%' ;
      this.rows = [];
      if(design){//设计问题
        this.init();
      }
    }
    /**
     * 验证答题
     * @param an
     * @returns {boolean}
     */
    anValid(an) {
      super.anValid(an);
      if (!this.required) {
        return true
      }
      if (Fun.isNull(an[this.varName])) {
        this.errInfo = "这道题必须回答哦！"
        return false
      }
      this.errInfo = ""
      return true
    }
    /**
     * 答题界面样式等配置
     */
    anInit(){
      super.anInit();
      if (this.device == 'pc') {
        this.width = this.pcWidth ;
      }else{
        this.width = this.phoneWidth ;
      }
    }
    /**
     * 答题时选项随机
     */
    anRandom(){
      super.anRandom();
      if (this.random) {
        Fun.optionRandom(this.rows)
      }
    }

    getVarNames(){
      let varSet = new Set();
      varSet.add(this.varName)
      return varSet
    }

    init(){
      let labels = ['选项1','选项2']
      let rate = null
      for(let label of labels){
        rate = new RadioSelectOptionClass();
        rate.htmlLabel = label
        this.rows.push(rate)
      }
    }
    addOption(index) {
      let option = new RadioSelectOptionClass();
      option.htmlLabel = "选项" + (this.rows.length+1);
      option.val = 'valR'+(++inc);
      if (index == -1) {//尾部插入
        this.rows.push(option)
      } else {//之后插入
        this.rows.splice(index + 1, 0, option);
      }
    }
    delOption(index){
      if (this.rows.length > 1) {
        this.rows.splice(index, 1)
      }
    }
    batchAddOptions(batchLabels){
      batchLabels.forEach(label => {
        let option = new RadioSelectOptionClass();
        option.htmlLabel = label;
        this.rows.push(option)
      })
    }

    /**
     * 获取批量显示的选项标签,用于打开批量增加选项对话框的默认值
     */
    getBatchLabels(){
      let htmlLabels = []
      this.rows.forEach((option) => {
        let htmlLabel = option.htmlLabel;
        if (!Fun.isNull(htmlLabel)) {
          htmlLabels.push(htmlLabel)
        }
      })
      return htmlLabels ;
    }

    /**
     * 上下移动选项
     * @param index
     * @param flag
     */
    moveOption(index,flag='pre'){
      let currOption = this.rows[index];
      if(flag==='pre'){//上移
        this.rows.splice(index, 1);//删除
        this.rows.splice(index - 1, 0, currOption);//再插入
      }else{
        this.rows.splice(index, 1);//删除
        this.rows.splice(index + 1, 0, currOption);//再插入
      }
    }
  }
  export default RadioSelectClass




