import Vue from "vue";
import Router from "vue-router";
import IndexDati from "@/components/an/IndexDati";
import UnValid from "@/components/an/UnValid";

Vue.use(Router)

const router = new Router({
  routes: [
    {path: '/', redirect: '/IndexDati'},
    {
      path: '/IndexDati',
      name: 'IndexDati',
      components: {default: IndexDati},
    },
    {
      path: '/UnValid',
      name: 'UnValid',
      components: {default: UnValid},
    }
  ],
})

router.beforeEach((to, from, next) => {
  if (to.matched.length === 0) {
    return next("/IndexDati")
  } else {
    next(); //如果匹配到正确跳转
  }
});
export default router;



