<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="optionSum">
        <div v-if="ques.optionSumShow&&ques.anHasOptionSum()">
          {{ques.anOptionSumInfo()}}
        </div>
      </div>
      <div slot="viewQues">
        <div :style="{width:ques.width}">
        <template v-for="(item,index) in ques.rows">
          <div v-html="item.htmlGroup"></div>
          <div style="height:8px"></div>
          <div style="display:flex;align-items: center;">
            <div :style="{width:ques.width1}" style="position:relative;display: flex;align-items: center">
              <div v-if="survey.an[item.varName]!==undefined" @click="unCheckClick(item)"
                   class="sortMask" style="cursor:pointer;">
                <span style="margin-left:8px;font-weight: bold">{{survey.an[item.varName]}}</span>
              </div>
              <div @click="checkClick(item)"
                   style="display: flex;align-items:center;cursor:pointer;width: 100%">
                <i class='iconfont'
                   style="font-size:26px;color:#4b8af7;">&#xe74e;</i>
                 <span style="width:25px">&nbsp;</span>&nbsp;
                <div style="width:95%" v-html="item.htmlLabel"></div>
              </div>
            </div>
            <div style="width:5px"></div>
            <div v-if="item.fillFlag" style="display: flex">
              <div style="margin-left:5px">
                <input v-model="survey.an[item.varNameT]"
                       :style="{width:ques.width}"
                       class="underline" type="text">
              </div>
              <div v-if="item.fillRequired" class="required"></div>
            </div>
          </div>
        </template>
        </div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultMulSortView',
    components: {
      ViewQuesT
    },
    props: ['survey', 'ques'],
    data () {
      //for (let item of this.ques.rows) {
        //this.$set(this.survey.an, item.varName, undefined)
      //}
      return {}
    },

    mounted(){
    },
    methods: {
      checkClick(currItem){
        let sort = 1;
        for (let item of this.ques.rows) {
          if (!this.Fun.isNull(this.survey.an[item.varName])) {
            sort++;
          }
        }
        this.$set(this.survey.an,currItem.varName,sort)
       // this.survey.an[currItem.varName] = sort;
      },
      unCheckClick(currItem){
        let currSort = this.survey.an[currItem.varName];
        this.survey.an[currItem.varName] = undefined;
        for (let item of this.ques.rows) {
          if (!this.Fun.isNull(this.survey.an[item.varName])) {
            if (this.survey.an[item.varName] > currSort) {
              this.survey.an[item.varName] = this.survey.an[item.varName] - 1
            }
          }
        }
      },

    },
    computed: {},
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  .sortMask {
    /**display: none;*/
    position: absolute;
    color: #f7ba2a;
    line-height: 35px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    /**opacity: 0.9;*/
    z-index: 100;
  }
</style>



