  import {BaseQuesClass} from './BaseClass'
  import Fun from '../Fun'
  /**
   * 填空题
   */
  class InputScrollClass extends BaseQuesClass {
    constructor(design=true) {
      super();
      this.quesType = 'InputScroll';
      this.htmlTitle = '<p><strong>滑动题</strong></p>';
      this.leftTitle = '';//左边标题
      this.rightTitle = '';//右边标题
      this.varName = 'D_'+Fun.getInc();
      this.fieldName = this.varName;
      this.minVal = 0;
      this.maxVal = 100 ;
      this.width = '100%' ;
      this.pcWidth = '600px' ;
      this.phoneWidth = '100%' ;

    }
    /**
     * 验证答题
     * @param an
     * @returns {boolean}
     */
    anValid(an){
      if (this.required) {
        if (Fun.isNull(an[this.varName])) {
          this.errInfo = "这道题必须回答哦！"
          return false
        }
      }
      this.errInfo = ""
      return true
    }
    /**
     * 答题界面样式配置
     */
    anInit(){
      super.anInit();
      if(this.device=='pc'){
        this.width = this.pcWidth ;
      }else{
        this.width = this.phoneWidth ;
      }
    }
    getVarNames(){
      let varSet = new Set();
      varSet.add(this.varName)
      return varSet
    }
  }
  export default InputScrollClass




