<template>
  <div>
    <div style="width:100%;height:100%;" v-if="show">
      <dati :survey="survey"></dati>
    </div>
    <el-dialog :show-close="false" :width="survey.device=='pc'?'35%':'93%'"
               :visible.sync="ctl.status=='T'">
      <div style="height:5px"></div>
      <div v-if="ctl.code=='wx10'" style="text-align: center">
        <div class="thumbnail">
          <img :src="qrcodeImg" class="img-thumbnail"
               style="width:256px;height:256px;">
        </div>
        <div><span>{{ctl.msg}}</span></div>
      </div>

      <!--非微信环境不会出现授权页面-->
      <div v-else-if="ctl.code=='wx11'||ctl.code=='wx12'">
        <div style="text-align:center;margin-bottom:10px;">
          <img src="../../assets/img/weixin.png" style="vertical-align:middle;width:30px;height:30px;">
          <span style="color:#9c9c9c;font-size:18px;">请先登录再填写问卷</span>
        </div>
        <div v-loading="wxLoginDisabled">
          <div>
            <el-button type="primary" @click="loginWx"
                       style="width:100%;font-size:22px" round>一键登录
            </el-button>
          </div>
          <div v-if="ctl.code=='wx12'" style="margin-top:10px;text-align: right">
            <el-button type="text" @click="skipWx"
                       style="font-size:18px" round>跳过登录
            </el-button>
          </div>
        </div>
      </div>
      <div v-else>
        <span style="color:#E6A23C">{{ctl.msg}}</span>
      </div>
      <div style="height:5px"></div>
    </el-dialog>
    <div v-if="error" style="margin:auto;width:30%;margin-top:30px">
      <el-card :body-style="{ padding: '30px' }"
               style="margin:auto;text-align:center">
        <div class="thumbnail">
          <img src="../../assets/img/error.png" class="img-thumbnail">
        </div>
        <div style="padding: 14px;">
          <span>好吃的汉堡</span>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>

  import Dati from './Dati.vue'

  export default {
    name: 'IndexDati',
    components: {
      Dati
    },

    data () {
      return {
        survey: new this.SurveyClass(),
        qrcodeImg: '',
        show: false,
        error: false,
        wxLoginDisabled: false,
        ctl: {
          code: '',
          status: '',
          msg: '',
        },
        info:{}
      }
    },

    mounted(){
      this.handleReqParam();//处理浏览器请求参数，问卷id，测试还是正式
      this.survey.device = this.Fun.getDevice();
      let qrcodeImg = this.baseURL + "pub/qrcodeImg?surveyId=" + this.survey.surveyId + "&qrSize=256";

      this.qrcodeImg = qrcodeImg

      this.openPage();

    },
    methods: {
      openPage(){
        //取问卷和问题，构建答题结构
        let _reqObj = {
          url: 'an/datiSurveyQuessData',
          params: {surveyId: this.survey.surveyId}
        }
        return this.SERVICE2(_reqObj).then(res => {
          this.survey.initSurveyStructure(res.data.res);
          document.title = this.survey.title;
          SYSCFG.wx.wxDesc = this.survey.title;
          let datiClass = new this.DatiClass(this.survey)
          //处理当前用户已经进行到那一页,包括从续答缓层检查提取
          this.survey.version = res.data.res.version
          datiClass.handleCurrPageIndex();
          datiClass.initAfterSurveyData();
          this.show = true;
          if (res.data.status === 'T') {
            this.setCtlCfg(res)
          }
          //加载历史样本答案
          if(this.survey.requiredWx){
            let wxCfg = this.survey.wxCfg
            if(wxCfg.login&&wxCfg.update){
              if(res.data.res.hisAn){
                this.survey.an = res.data.res.hisAn
                this.survey.anId = res.data.res.anId
              }
            }
          }
        }).catch((res) => {
          if (res.data.status === 'T') {
            this.setCtlCfg(res)
          } else {
            this.error = true
          }
        })
      },

      loginWx() {
        let hasPageAuth = Cookie.Get("wxPageAuth_" + this.survey.surveyId);
        if (this.Fun.isNull(hasPageAuth)) {//没有授权用户
          this.wxLoginDisabled = true;
          window.location.href = 'wx/wxPageAuthRedirect?surveyId=' + this.survey.surveyId
        } else {//不用再调微信授权接口了
          this.ctl.status = ''
        }
      },
      skipWx() {
        this.survey.wxSkip = true;
        this.ctl.status = ''
      },

      setCtlCfg(res){
        this.ctl.code = res.data.code;
        this.ctl.msg = res.data.msg;
        this.ctl.status = res.data.status;
      },
      /**
       * 处理浏览器的请求参数
       */
      handleReqParam(){
        this.survey.model = 1;//答题模式，随机数等就可以生效等
        this.survey.surveyId = this.Fun.getUrlParam('sd');//surveyId

      },
    }
  }
</script>

<style type="less">
  * {
    margin: 0;
    padding: 0;
    background-repeat: no-repeat;
    font-family: "微软雅黑", "Lucida Grande", "Helvetica", "Arial";
  }

  .ques {
    font-size: 15px;
    font-family: "微软雅黑", "隶书", "宋体", "仿宋";
  }

  .ques p {
    margin: 2px 0;
    line-height: 26px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  .err {
    color: white;
    border-radius: 6px;
    background-color: red;
    font-size: 11px;
    margin-top: 5px;
  }

  .required {
    color: red;
    margin-left: 3px
  }

  .required::after {
    margin-right: 3px;
    content: '*';
  }

  .spacing {
    height: 8px
  }

  label.activerow {
    padding: 3px 5px;
  }

  .activerow:hover {
    background: rgba(128, 126, 118, 0.05);
    border-radius: 8px;
    cursor: pointer;
  }

  .underline {
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 1px #cccccc solid;
    padding: 2px 3px;
    background-color: rgba(56, 59, 128, 0);
  }

  span.textStyle {
    color: #5a5e66
  }

  .el-form-item {
    margin-bottom: 15px;
  }

  /** 未编辑的颜色*/
  .unEdit {
    color: #99A9BF;
  }

  label {
    margin-bottom: 1px;
    margin-top: 1px;
    font-weight: normal;
    font-family: "微软雅黑", "Lucida Grande", "Helvetica", "Arial";
  }

  a:link {
    text-decoration: none;
    color: #333;
    background-color: transparent;
  }

  button.btn-link {
    text-decoration: none !important;
    outline: none !important;
  }

  button.btn-link:hover {
    font-weight: bold;
  }

  div.ckeditor div.cke_editable_inline {
    padding-top: 3px;
    border: 1px solid gainsboro;
    border-radius: 5px;
  }

  div.ckeditor div.cke_editable_inline p {
    margin: 3px 0 3px 5px;
  }

  span.cke_combo_text {
    width: 36px;
  }

  /**
  div.ckeditor div.cke_editable p {
    margin: 5px 0 5px 5px;
  }*/
  .el-checkbox__label {
    padding-left: 3px;
  }

  .el-radio__label {
    padding-left: 3px;
  }

  .el-dialog__header {
    padding: 15px 20px 0;
  }

  .el-dialog__body {
    padding: 6px 20px;
  }

  .el-dialog__footer {
    padding: 5px 20px 10px;
  }

  .footer {
    background-color: #F5F5F5;
    /**border-top: 1px solid #E5E5E5;*/
    color: #999999;
    padding: 10px 0;
    text-align: center;
    height: 60px;
  }

  .thumbnail {
    border: 0px solid #DDDDDD;
    border-radius: 0px;
    margin-bottom: 2px;
    margin-top: 1px;
    padding: 2px;
    box-shadow: none;
  }

  .unBorder {
    outline: none !important;
    border: none;
  }

  dd.ellipsis {
    white-space: nowrap;
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
  }

  div.ellipsis {
    white-space: nowrap;
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
  }
</style>



