<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        <div :style="{width:ques.width}">
          <el-select style="width:99%" v-model="survey.an[ques.varName]" placeholder="--请选择--">
            <el-option :label="Fun.html2Text(item.htmlLabel)" :value="item.val" v-for="(item,index) in ques.rows" :key="index"></el-option>
          </el-select>
        </div>
        <div style="height:3px"></div>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'SelectRadioView',
    components: {
      ViewQuesT,
    },
    props: ['survey', 'ques'],
    data () {
      return {}
    },
    mounted(){
    },
    methods: {},
    computed: {},
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  table td {
    padding: 1px 3px;
  }
</style>



