<template>
  <div>
    <div v-if="showTime" class="letTime topWidth">
      <div>
        <span style="color:#F56C6C">剩余时间&nbsp;<i class="el-icon-time"></i>：{{syTime}}</span>
      </div>

    </div>
    <template v-for="(ques,quesIndex) in survey.pages[survey.currPageIndex].quess">
      <div :id="'an'+ques.quesId" v-if="ques.getVisible(survey.an,survey.varNameSet)">
        <div style="height:10px;"></div>
        <view-ques :key="ques.quesId" :survey="survey" :ques="ques" :device="survey.device"
                   style="width:100%;margin: auto"></view-ques>
        <div style="height:15px;"></div>
      </div>
    </template>
    <div style="height:15px;clear: both"></div>
    <div class="submitBt" style="width:98%;margin:auto;"
         :style="{'text-align':survey.cfg.submitTextAlign}">
      <el-button @click="survey.anValidSubmit()" type="primary"
                 style="border-color:transparent;" size="medium"
                 :style="{'background-color':survey.cfg.submitBgColor,width:survey.cfg.submitWidth}"
                 v-html="survey.cfg.submitContent">
      </el-button>
    </div>
    <div v-if="survey.cfg.back&&survey.pageStack.length>0">
      <div style="height:10px;"></div>
      <div class="backBt" style="width:98%;margin:auto" :style="{'text-align':survey.cfg.backTextAlign}">
        <el-button @click="survey.anBackSubmit()"
                   style="border-color: transparent;padding:6px 10px"
                   :style="{'background-color': survey.cfg.backBgColor,width:survey.cfg.backWidth}"
                   v-html="survey.cfg.backContent">
        </el-button>
      </div>
    </div>
    <div style="height:10px;"></div>
  </div>
</template>

<script>

  import ViewQues from '../ques/ViewQues'
  export default {
    name: 'AnViewQues',
    props: ['survey'],
    components: {
      ViewQues
    },
    data () {
      return {
        syTime:'',
        showTime:false,
        interval:null,
      };
    },
    mounted(){
    //  clearInterval(interval );//停止
      if(this.survey.dateCtl&&!this.Fun.isNull(this.survey.endDate)){
        var leftTime = (this.survey.endDate - new Date().getTime());
        if(leftTime>2000){
          this.intervalTime()
        }
      }

    },
    methods: {
      intervalTime(){
        this.interval = window.setInterval(this.timediff,2000);
      },
      timediff(){
        var leftTime = (this.survey.endDate - new Date().getTime());
        if(leftTime>4000&&leftTime<=1800*1000) {
          var days = parseInt(leftTime / 1000 / 60 / 60 / 24, 10); //计算剩余的天数
          var hours = parseInt(leftTime / 1000 / 60 / 60 % 24, 10); //计算剩余的小时
          var minutes = parseInt(leftTime / 1000 / 60 % 60, 10);//计算剩余的分钟
          var seconds = parseInt(leftTime / 1000 % 60, 10);//计算剩余的秒数
          days = this.checkTime(days);
          hours = this.checkTime(hours);
          minutes = this.checkTime(minutes);
          seconds = this.checkTime(seconds);
          this.syTime = hours + ":" + minutes + ":" + seconds;
          if(!this.showTime){
            this.showTime= true
          }
        }else{
            if(leftTime<=4000){
              this.showTime= false
              clearInterval(this.interval );//停止
              this.survey.unValidSubmit().then(() => {
                this.$router.push({path: '/UnValid', query: {}});
              })
            }
        }
      },
      checkTime(i){
        //将0-9的数字前面加上0，例1变为01
        if (i < 10) {
          i = "0" + i;
        }
        return i;
      }
    },
    computed: {},
    watch: {
      //路由变化监听
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  div.submitBt button p {
    margin: 0 3px;
  }

  div.backBt button p {
    margin: 0;
  }
</style>
<style scoped lang="less">
  .letTime{
    position: fixed;
    top:0;
    line-height:1.8;
    background-color: #FFF6DA;
    text-align: center;
  }
  @media screen and (min-width: 600px) {
    .topWidth{
      width: 910px;
    }
  }
  @media screen and (max-width: 600px) {
    .topWidth{
      width: 100%;
    }
  }

</style>



