import {BaseOptionClass, BaseQuesClass} from "./BaseClass";
import Fun from "../Fun";

let inc = 0;
/**
 * 选项
 */
class MatrixRateRowOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.varName = 'H_'+Fun.getInc();
    this.fieldName = this.varName;
    this.htmlGroup = '';
    this.noRandom = false;//选项固定，在使用随机显示选项才起作用
  }
}
class MatrixRateColOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.val = Fun.getInc();
  }
}
/**
 * 矩阵量表题
 */
class MatrixRateClass extends BaseQuesClass {
  constructor(design = true) {
    super();
    this.quesType = 'MatrixRate';
    this.htmlTitle = '<p><strong>矩阵量表题</strong></p>';
    this.random = false;//选项随机
    this.width = '' ;
    this.pcWidth = '55%' ;
    this.phoneWidth = '100%' ;

    this.unActiveColor = '#c6d1de';
    this.pcUnActiveColor = '#c6d1de';
    this.phoneUnActiveColor = '#c6d1de';

    this.activeColor = '#f7ba2a';
    this.pcActiveColor = '#f7ba2a';
    this.phoneActiveColor = '#f7ba2a';

    this.icon = '&#xe6d8;';
    this.pcIcon = '&#xe6d8;';
    this.phoneIcon = '&#xe6d8;';

    this.iconSize = 26;
    this.pcIconSize = 26;
    this.phoneIconSize = 26;

    this.rows = [];
    this.cols = [];
    if (design) {//设计问题
      this.init();
    }
  }

  /**
   * 验证答题
   * @param an
   * @returns {boolean}
   */
  anValid(an) {
    if (!this.required) {
      return true
    }
    let noSuccess = false
    for (let item of this.rows) {
      if (Fun.isNull(an[item.varName])) {
        noSuccess = true
        break;
      }
    }
    if (noSuccess) {
      this.errInfo = "每一行都需要回答哦！"
      return false
    }
    this.errInfo = ""
    return true
  }

  /**
   * 答题时选项随机
   */
  anRandom() {
    super.anRandom();
    if (this.random) {
      Fun.optionRandom(this.rows)
    }
  }

  /**
   * 答题界面样式配置
   */
  anInit() {
    super.anInit();
    if (this.device == 'pc') {
      this.width = this.pcWidth ;
      this.unActiveColor = this.pcUnActiveColor ;
      this.activeColor = this.pcActiveColor ;
      this.icon = this.pcIcon ;
      this.iconSize = this.pcIconSize ;
    }else{
      this.width = this.phoneWidth ;
      this.unActiveColor = this.phoneUnActiveColor ;
      this.activeColor = this.phoneActiveColor ;
      this.icon = this.phoneIcon ;
      this.iconSize = this.phoneIconSize ;
    }
  }

  getVarNames() {
    let varSet = new Set();
    for (let item of this.rows) {
      varSet.add(item.varName)
    }
    return varSet
  }

  init() {
    let labels = ['<p>功能</p>', '<p>外观</p>']
    let rate = null
    for (let label of labels) {
      rate = new MatrixRateRowOptionClass();
      rate.htmlLabel = label
      this.rows.push(rate)
    }

    labels = ['<p>很不满意</p>', '<p>2</p>', '<p>3</p>', '<p>4</p>', '<p>很满意</p>']
    for (let i = 0; i < labels.length; i++) {
      rate = new MatrixRateColOptionClass();
      rate.val = "" + (i + 1)
      rate.htmlLabel = labels[i]
      this.cols.push(rate)
    }
  }

  addOption(index, flag) {
    if (flag == 'row') {
      let option = new MatrixRateRowOptionClass();
      option.htmlLabel = "<p>选项" + (this.rows.length + 1)+"</p>";
      if (index == -1) {//尾部插入
        this.rows.push(option)
      } else {//之后插入
        this.rows.splice(index + 1, 0, option);
      }
    } else {
      let option = new MatrixRateColOptionClass();
      option.htmlLabel = "<p>选项" + (this.cols.length + 1)+"</p>";
      if (index == -1) {//尾部插入
        this.cols.push(option)
      } else {//之后插入
        this.cols.splice(index + 1, 0, option);
      }
    }


  }

  delOption(index, flag) {
    if (flag == 'row') {
      if (this.rows.length > 1) {
        this.rows.splice(index, 1)
      }
    } else {
      if (this.cols.length > 1) {
        this.cols.splice(index, 1)
      }
    }

  }

  batchAddOptions(batchLabels, flag) {
    if (flag == 'row') {
      batchLabels.forEach(label => {
        let option = new MatrixRateRowOptionClass();
        option.htmlLabel = label;
        this.rows.push(option)
      })
    } else {
      batchLabels.forEach(label => {
        let option = new MatrixRateColOptionClass();
        option.htmlLabel = label;
        this.cols.push(option)
      })
    }

  }

  /**
   * 获取批量显示的选项标签,用于打开批量增加选项对话框的默认值
   */
  getBatchLabels(flag) {
    if (flag == 'row') {
      let htmlLabels = []
      this.rows.forEach((option) => {
        let htmlLabel = option.htmlLabel;
        if (!Fun.isNull(htmlLabel)) {
          htmlLabels.push(htmlLabel)
        }
      })
      return htmlLabels;
    } else {
      let htmlLabels = []
      this.cols.forEach((option) => {
        let htmlLabel = option.htmlLabel;
        if (!Fun.isNull(htmlLabel)) {
          htmlLabels.push(htmlLabel)
        }
      })
      return htmlLabels;
    }

  }

  /**
   * 上下移动选项
   * @param index
   * @param flag
   */
  moveOption(index, optionFlag, flag = 'pre') {
    if (optionFlag == 'row') {
      let currOption = this.rows[index];
      if (flag === 'pre') {//上移
        this.rows.splice(index, 1);//删除
        this.rows.splice(index - 1, 0, currOption);//再插入
      } else {
        this.rows.splice(index, 1);//删除
        this.rows.splice(index + 1, 0, currOption);//再插入
      }
    } else {
      let currOption = this.cols[index];
      if (flag === 'pre') {//上移
        this.cols.splice(index, 1);//删除
        this.cols.splice(index - 1, 0, currOption);//再插入
      } else {
        this.cols.splice(index, 1);//删除
        this.cols.splice(index + 1, 0, currOption);//再插入
      }
    }

  }
}
export default MatrixRateClass




