<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        <table :style="{width:ques.width}">
          <template v-for="(item,index) in ques.rows">
            <tr>
              <td>
                <div v-html="item.htmlGroup"></div>
              </td>
            </tr>
            <tr>
              <td>
                <div style="display: flex;align-items:center;">
                  <div style="display: flex;align-items:center;">
                    <div v-html="item.htmlLabel"></div>
                    <span style="width:15px"></span>
                    <el-input-number v-model="survey.an[item.varName]" style="width:105px"
                                     :min="ques.minVal" :max="ques.maxVal"></el-input-number>
                  </div>
                  <span style="width:25px"></span>
                  <el-slider :style="{width:ques.width1}"
                    v-model="survey.an[item.varName]" :min="ques.minVal" :max="ques.maxVal"></el-slider>
                  </div>
              </td>
            </tr>
          </template>
        </table>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultMatrixScaleView',
    components: {
      ViewQuesT
    },
    props: ['survey', 'ques'],
    data () {

      return {}
    },

    mounted(){
    },
    methods: {},
    computed: {},
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  table td {
    padding: 3px 5px 3px 5px;
  }

</style>



