<template>
  <div class="quesTemp" :style="{'background-color': survey.cfg.quesBackgroundColor}">
    <div v-html="htmlTitle" style="margin-top:5px;width:98%"></div>
    <div v-show="ques.errInfo.length>0" style="display:flex">
      <div class="err">
        &nbsp;&nbsp;{{ques.errInfo}}&nbsp;
      </div>
    </div>
    <div class="spacing"></div>
    <div v-if="ques.descFlag" v-html="ques.htmlDesc"></div>
    <div>
      <div :style="{'padding-left':ques.paddingLeft}">
        <slot name="viewQues"></slot>
      </div>
    </div>
    <div v-if="ques.tipsFlag">
      <div class="spacing"></div>
      <div v-html="ques.htmlTips"></div>
    </div>
  </div>

</template>

<script>


  export default {
    name: 'ViewQuesT',
    components: {},
    props: ['survey', 'ques'],
    data () {
      return {}
    },
    created(){

    },
    mounted(){
    },
    beforeDestroy(){

    },
    methods: {

    },
    computed: {

      htmlTitle(){
        let pos = this.ques.htmlTitle.lastIndexOf("</p>")
        let insertString = '';
        if (this.ques.required && this.ques.requiredShow ) {
          insertString = '<span style="color:red">&nbsp;*</span>';
        }
        if (this.ques.optionSumShow && this.ques.anHasOptionSum()) {
          insertString +='<span style="color:#409EFF">&nbsp;'+this.ques.anOptionSumInfo()+'</span>';
        }
        if(pos>-1) {
          let len = this.ques.htmlTitle.length;
          let left = this.ques.htmlTitle.substring(0, pos);
          let right = this.ques.htmlTitle.substring(pos, len);//
          if(insertString.length>0){
            return left +  insertString + right ;
          }else{
            return this.ques.htmlTitle ;
          }
        }else{
            return this.ques.htmlTitle+insertString ;
        }
      }
    },
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>
  .quesTemp {
    border-radius: 5px;
    padding: 15px 25px;
  }

</style>



