<template>
  <div>
    <view-ques-t :survey="survey" :ques="ques">
      <div slot="viewQues">
        <table :style="{width:ques.width}">
          <tr>
            <td></td>
            <td v-for="(itemCol,indexCol) in ques.cols" style="text-align:center">
              <div v-html="itemCol.htmlLabel"></div>
            </td>
          </tr>
          <template v-for="(itemRow,indexRow) in ques.rows">
            <tr>
              <td colspan="3">
                <div v-html="itemRow.htmlGroup"></div>
              </td>
            </tr>
            <tr>
              <td>
                <div v-html="itemRow.htmlLabel"></div>
              </td>
              <template v-for="(itemCol,indexCol) in ques.cols">
                <td style="text-align:center">
                  <el-select :style="{width:ques.width1}" v-model="survey.an[itemRow.varName+itemCol.varName]">
                    <el-option
                      v-for="item in ques.list"
                      :key="item.val"
                      :label="item.htmlLabel"
                      :value="item.val">
                    </el-option>
                  </el-select>
                </td>
              </template>
            </tr>
          </template>
        </table>
      </div>
    </view-ques-t>
  </div>
</template>

<script>
  import ViewQuesT from '../../ViewQuesT'
  export default {
    name: 'DefaultMatrixSelectView',
    components: {
      ViewQuesT
    },
    props: ['survey', 'ques'],
    data () {
      return {}
    },

    mounted(){
    },
    methods: {},
    computed: {},
    watch: {}
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>
<style scoped>
  table td {
    padding: 3px 5px 3px 5px;
  }

</style>



