import {BaseQuesClass,BaseOptionClass} from './BaseClass'
import Fun from '../Fun'

  /**
   * 单选题选项
   */
  class RadioOptionClass extends BaseOptionClass {
    constructor() {
      super();
      this.val = Fun.getValInc();
      this.htmlGroup = '';
      this.dft = false;//默认选中
      this.noRandom = false;//选项固定，在使用随机显示选项才起作用
      this.imgUrl = Fun.getLogoUrl();
    }
  }
  /**
   * 单选题
   */
  class RadioClass extends BaseQuesClass {
    constructor(design=true) {
      super();
      this.quesType = 'Radio';
      this.htmlTitle = '<p><strong>单选题</strong></p>';
      this.varName = 'P_'+Fun.getInc();
      this.varNameT = 'T'+this.varName;
      this.fieldName = this.varName;
      this.fieldNameT = this.varNameT;
      this.random = false;//选项随机
      this.fillFlag = false; //允许填空
      this.fillRequired = false;//允许填空必填
      this.width = '' ;
      this.width1 = '';//标签宽度
      this.horCol = '1';//每行列数
      this.pcHorCol = '1' ;
      this.phoneHorCol = '1' ;

      this.imgWidth = '' ;//图片宽度
      this.imgHeight = '';//
      this.imgShape = '';

      this.pcImgShape = 'img-rounded' ;
      this.phoneImgShape = 'img-rounded' ;

      this.pcImgWidth = '150px' ;
      this.pcImgHeight = '150px' ;

      this.phoneImgWidth = '110px' ;
      this.phoneImgHeight = '110px' ;

      this.pcWidth = '95%' ;
      this.pcWidth1 = '95%' ;

      this.phoneWidth = '100%' ;
      this.phoneWidth1 = '100%' ;
      //this.pcStyles.push(...[{code: 'p01', label: '下拉列表'}]);
      this.rows = [];
      if(design){//设计问题
        this.init();
      }
    }

    /**
     * 验证答题
     * @param an
     * @returns {boolean}
     */
    anValid(an){
      if (this.required) {
        if (Fun.isNull(an[this.varName])) {
          this.errInfo = "这道题必须回答哦！"
          return false
        }
        if (this.fillFlag && this.fillRequired) {
          if (Fun.isNull(an[this.varNameT])) {
            this.errInfo = "标注*文本框需要填写！"
            return false
          }
        }
      }
      this.errInfo = ""
      return true ;
    }
    /**
     * 单选题默认值
     * @param an
     */
    anDefaultVal(VUE,an){
      super.anDefaultVal(an);
      for (let item of this.rows) {
        if (item.dft) {
          //an[this.varName] = item.val
          VUE.$set(an, this.varName, item.val)
          break;
        }
      }
    }
    getVarNames(){
      let varSet = new Set();
      varSet.add(this.varName)
      varSet.add(this.varNameT)
      return varSet
    }

    /**
     * 答题界面样式配置
     */
    anInit(){
      super.anInit();
      if (this.device == 'pc') {
        this.width = this.pcWidth ;
        this.width1 = this.pcWidth1 ;
        this.horCol = this.pcHorCol;
        this.imgWidth = this.pcImgWidth ;
        this.imgHeight = this.pcImgHeight ;
        this.imgShape = this.pcImgShape ;
      }else{
        this.width = this.phoneWidth ;
        this.width1 = this.phoneWidth1 ;
        this.horCol = this.phoneHorCol;
        this.imgWidth = this.phoneImgWidth ;
        this.imgHeight = this.phoneImgHeight ;
        this.imgShape = this.phoneImgShape ;
      }
    }
    /**
     * 答题时选项随机
     */
    anRandom(){
      super.anRandom();
      if (this.random) {
        Fun.optionRandom(this.rows)
      }
    }


    init(){
      let labels = ['<p>选项1</p>','<p>选项2</p>']
      let rate = null
      for(let label of labels){
        rate = new RadioOptionClass();
        rate.htmlLabel = label
        this.rows.push(rate)
      }
    }
    addOption(index) {
      let option = new RadioOptionClass();
      option.htmlLabel = "<p>选项" + (this.rows.length+1)+"</p>";
      if (index == -1) {//尾部插入
        this.rows.push(option)
      } else {//之后插入
        this.rows.splice(index + 1, 0, option);
      }
    }
    delOption(index){
      if (this.rows.length > 1) {
        this.rows.splice(index, 1)
      }
    }
    batchAddOptions(batchLabels){
      batchLabels.forEach(label => {
        let option = new RadioOptionClass();
        //console.log(option.val)
        option.htmlLabel = "<p>"+label+"</p>";
        this.rows.push(option)
      })
    }

    /**
     * 获取批量显示的选项标签,用于打开批量增加选项对话框的默认值
     */
    getBatchLabels(){
      let htmlLabels = []
      this.rows.forEach((option) => {
        let htmlLabel = option.htmlLabel;
        if (!Fun.isNull(htmlLabel)) {
          htmlLabels.push(htmlLabel)
        }
      })
      return htmlLabels ;
    }

    /**
     * 上下移动选项
     * @param index
     * @param flag
     */
    moveOption(index,flag='pre'){
      let currOption = this.rows[index];
      if(flag==='pre'){//上移
        this.rows.splice(index, 1);//删除
        this.rows.splice(index - 1, 0, currOption);//再插入
      }else{
        this.rows.splice(index, 1);//删除
        this.rows.splice(index + 1, 0, currOption);//再插入
      }
    }
  }
  export default RadioClass




