import Fun from "./Fun.js";
import Storage from "./Storage.js";

class DatiClass {
  constructor(_survey) {
    this.survey = _survey;
  }

  /**
   * 问卷数据初始后，需要进行的操作
   */
  initAfterSurveyData() {
    this.survey.device = Fun.getDevice();
    this.survey.anInit();
    this.survey.initVarNames();//初始化问卷所有变量
    document.body.style.backgroundColor = this.survey.cfg.surveyBackgroundColor;
  }

  /**
   * 处理当前用户已经进行到那一页，包括续答的处理
   */
  handleCurrPageIndex() {
    let hisPageIndex = this.getHisPageIndexFromStorage();//从断点续答缓层里检查下一页码
    if (Fun.isNull(hisPageIndex)) {//没有续答页面索引
      if (this.survey.startQues.use) {
        this.survey.currPageIndex = -1
      } else {
        this.survey.currPageIndex = 0;
      }
    } else {
      this.survey.currPageIndex = hisPageIndex;
    }
  }

  /**
   * 支持断点续答时，取出将要展开的页码和以前之前的答案
   */
  getHisPageIndexFromStorage() {
    let hisPageIndex = null;
    if (this.survey.keep) {//支持断点续答
      let surveyKey = this.survey.surveyId

      let keepInfo = Storage.getStorage(surveyKey)
      if (!Fun.isNull(keepInfo)) {
        if (!Fun.isNull(keepInfo.version)) { //断点续答必须有版本号
          let hisVersion = parseInt(keepInfo.version);
          if (hisVersion == this.survey.version) {//续答版本号需要等于当前版本号
            if (!Fun.isNull(keepInfo.hisPageIndex)) {
              hisPageIndex = parseInt(keepInfo.hisPageIndex);
              this.survey.an = keepInfo.an;//断点之前答案
              this.survey.anId = keepInfo.anId
              this.survey.pageStack = keepInfo.pageStack
            }
          }
        }
      }
      if (hisPageIndex == null && !Fun.isNull(keepInfo)) {//清除续答垃圾数据
        Storage.removeStorage(surveyKey);
      }
    }
    return hisPageIndex;
  }

  isWeixin() {
    let ua = navigator.userAgent.toLowerCase();
    let isWeixin = ua.indexOf('micromessenger') != -1;
    if (isWeixin) {
      return true;
    } else {
      return false;
    }
  }

}

export default DatiClass



